<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>Perform Benchmark Tests and Generate Evaluation Reports</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="protocol" prop="protocol">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.protocol"
              placeholder="please select protocol"
              filterable
            >
              <el-option
                v-for="(item, index) in protocolOptions"
                :key="index"
                :label="item"
                :value="item"
              />
            </el-select>
          </el-form-item>

          <el-form-item label="URL" prop="url">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.url"
              placeholder="example: http://localhost:8080/user/1"
            />
          </el-form-item>

          <el-form-item label="request method" prop="method">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.method"
              placeholder="please select method"
              filterable
            >
              <el-option
                v-for="(item, index) in methodOptions"
                :key="index"
                :label="item"
                :value="item"
              />
            </el-select>
          </el-form-item>

          <el-form-item
            v-if="ruleForm.method !== 'GET'"
            label="request body"
            prop="body"
          >
            <el-input
              style="width: 80%; font-size: medium"
              type="textarea"
              v-model="ruleForm.body"
              placeholder="please enter request body, support json, form-data, text"
              :autosize="{ minRows: 3, maxRows: 10 }"
            />
          </el-form-item>

          <el-form-item label="request headers" prop="headers">
            <el-input
              style="width: 80%; font-size: medium"
              type="textarea"
              v-model="ruleForm.headers"
              placeholder="Key : Value, multiple request headers separated by newline"
              :autosize="{ minRows: 1, maxRows: 5 }"
            />
          </el-form-item>

          <el-form-item label="concurrency" prop="concurrency">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.number="ruleForm.concurrency"
              placeholder="default value CPU cores * 3"
              type="number"
              min="0"
              @blur="resetIfZero('concurrency')"
            >
            </el-input>
          </el-form-item>

          <el-form-item label="execution strategy" prop="testType">
            <el-radio-group v-model="ruleForm.testType">
              <el-radio label="requests">request quantity</el-radio>
              <el-radio label="duration">duration</el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item
            v-if="ruleForm.testType === 'duration'"
            label="duration(s)"
            prop="duration"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.number="ruleForm.duration"
              placeholder="default value is 10"
              type="number"
              min="0"
              @blur="resetIfZero('duration')"
            />
          </el-form-item>

          <el-form-item
            v-if="ruleForm.testType === 'requests'"
            label="request quantity"
            prop="totalRequests"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.number="ruleForm.totalRequests"
              placeholder="default value is 5000"
              type="number"
              min="0"
              @blur="resetIfZero('totalRequests')"
            />
          </el-form-item>

          <el-form-item label="push target" prop="pushType">
            <el-checkbox
              v-model="prometheusChecked"
              @change="handlePushTypeChange('prometheus', $event)"
              >prometheus</el-checkbox
            >
            <el-checkbox
              v-model="customChecked"
              @change="handlePushTypeChange('custom', $event)"
              >customization</el-checkbox
            >
          </el-form-item>

          <el-form-item
            v-if="ruleForm.pushType === 'custom'"
            label="URL"
            prop="pushUrl"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.pushUrl"
              placeholder="example: http://localhost:7070/report"
            />
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Request method: <b>POST</b><br />
                Request body content in JSON format, example：
                <pre>
{
  "perf_test_id": 1756889770355402807,
  "url": "http://localhost:8080/post",
  "method": "POST",
  "body": "{\"name\":\"Alice\",\"age\":25}",
  "total_requests": 500000,
  "total_duration": 4.5,
  "success_count": 500000,
  "error_count": 0,
  "errors": [""],
  "qps": 111285.6,
  "avg_latency": 0.44,
  "p25_latency": 0,
  "p50_latency": 0.51,
  "p95_latency": 1.23,
  "min_latency": 0,
  "max_latency": 26.94,
  "total_sent": 12500000,
  "total_received": 24500000,
  "status_codes": {
    "200": 500000
  },
  "created_at": "2025-08-08T18:56:11.86+08:00"
}

Tip: The unit of total_duration is seconds, and
     the unit of xxx_latency is milliseconds.
</pre
                >
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item
            v-if="ruleForm.pushType === 'prometheus'"
            label="address"
            prop="prometheusUrl"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.prometheusUrl"
              placeholder="prometheus address example: http://localhost:9090"
            />
          </el-form-item>

          <el-form-item
            v-if="ruleForm.pushType === 'prometheus'"
            label="job name"
            prop="jobName"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.jobName"
              placeholder="please enter job name"
            />
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">Reset</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="btnRunPerftestShowDisabled"
            :loading="btnRunPerftestLoading"
            >Run</el-button
          >
          <el-button
            type="primary"
            size="default"
            @click="stopPerformanceTest"
            :disabled="btnStopPerftestShowDisabled"
            :loading="btnStopPerftestLoading"
            >Stop</el-button
          >
          <el-button size="default" v-show="isShowResultBtn" @click="showResult"
            >View Report</el-button
          >
        </div>
      </div>

      <el-dialog
        top="5vh"
        title="Performance Test Report"
        :visible.sync="showResultDialogVisible"
        width="66%"
        center
      >
        <el-input
          type="textarea"
          :rows="20"
          autosize
          readonly
          v-model="responseContent"
        >
        </el-input>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
export default {
  name: "PerformanceTest",
  data() {
    return {
      title: "Pressure test parameter configuration",
      path: "performanceTest",
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,

      btnRunPerftestShowDisabled: false,
      btnRunPerftestLoading: false,
      btnStopPerftestShowDisabled: true,
      btnStopPerftestLoading: false,

      responseContent: "",
      showResultDialogVisible: false,
      isShowResultBtn: false,

      customChecked: false,
      prometheusChecked: false,

      ruleForm: {
        protocol: "HTTP",
        url: "",
        method: "GET",
        body: "",
        headers: "",
        concurrency: "",
        testType: "",
        duration: "",
        totalRequests: "",
        pushType: "",
        pushUrl: "",
        prometheusUrl: "",
        jobName: "",
      },
      rules: {
        protocol: [
          {
            required: true,
            message: "please select protocol",
            trigger: "blur",
          },
        ],
        url: [
          { required: true, message: "please enter URL", trigger: "blur" },
          {
            pattern: /^(http|https):\/\/.+/,
            message: "please enter a valid URL address",
            trigger: "blur",
          },
        ],
        pushUrl: [
          {
            required: true,
            pattern: /^(http|https):\/\/.+/,
            message: "please enter a valid URL address",
            trigger: "blur",
          },
        ],
        prometheusUrl: [
          {
            required: true,
            pattern: /^(http|https):\/\/.+/,
            message: "please enter a valid URL address",
            trigger: "blur",
          },
        ],
        jobName: [
          {
            required: true,
            message: "please enter job name",
            trigger: "blur",
          },
        ],
      },

      protocolOptions: ["HTTP", "HTTP/2", "HTTP/3"],

      methodOptions: ["GET", "POST", "PUT", "DELETE", "PATCH"],
    };
  },
  created: function() {
    this.adaptiveWindows();

    this.responseContent = localStorage.getItem("PerformanceTestReport");
    if (this.responseContent !== null && this.responseContent.length > 0) {
      this.isShowResultBtn = true;
    }

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.protocol = data.data.protocol;
          this.ruleForm.url = data.data.url;
          this.ruleForm.method = data.data.method;
          this.ruleForm.body = data.data.body;
          this.ruleForm.testType = data.data.testType;
          this.ruleForm.pushType = data.data.pushType;
          this.ruleForm.pushUrl = data.data.pushUrl;
          this.ruleForm.prometheusUrl = data.data.prometheusUrl;
          this.ruleForm.jobName = data.data.jobName;
        }
        if (this.ruleForm.protocol === "") {
          this.ruleForm.protocol = "HTTP";
        } else {
          this.ruleForm.protocol = this.toUpperProtocol(this.ruleForm.protocol);
        }
        if (this.ruleForm.method === "") {
          this.ruleForm.method = "GET";
        }
        if (data.data.headers !== null && data.data.headers.length > 0) {
          this.ruleForm.headers = data.data.headers.join("\n");
        }
        if (data.data.worker > 0) {
          this.ruleForm.concurrency = data.data.worker;
        }
        if (data.data.totalRequests > 0) {
          this.ruleForm.totalRequests = data.data.totalRequests;
        }
        if (data.data.duration !== "") {
          this.ruleForm.duration = data.data.duration;
        }
        if (data.data.pushType === "custom") {
          this.customChecked = true;
        } else if (data.data.pushType === "prometheus") {
          this.prometheusChecked = true;
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.setRunPerftestBtnStatus(false, true);
          this.setStopPerftestBtnStatus(false, false);
          const apiUrl = appConfig.spongeServiceAddr + "/performanceTest";
          const protocolValue = this.toLowerProtocol(this.ruleForm.protocol);
          var arg = `perftest ${protocolValue} --url=${this.ruleForm.url}`;
          if (this.ruleForm.method === "") {
            this.ruleForm.method = "GET";
          }
          arg += ` --method=${this.ruleForm.method}`;
          if (this.ruleForm.headers) {
            const headersList = this.parseHeadersList();
            for (let header of headersList) {
              arg += ` --header=${header.key}:${header.value}`;
            }
          }
          if (this.ruleForm.method !== "GET" && this.ruleForm.body !== "") {
            if (arg.toLowerCase().includes("application/json")) {
              try {
                const obj = JSON.parse(this.ruleForm.body);
                let compact = JSON.stringify(obj);
                const body = compact.replace(/\n/g, "\\n").replace(/ /g, "%20");
                arg += ` --body=${body}`;
              } catch (err) {
                this.$message.error("JSON parse failed: " + err.message);
                this.resetBtnStatus();
                return false;
              }
            } else {
              arg += ` --body=${this.ruleForm.body}`;
            }
          }
          if (this.ruleForm.concurrency !== "") {
            arg += ` --worker=${this.ruleForm.concurrency}`;
          }
          if (this.ruleForm.testType === "duration") {
            if (this.ruleForm.duration !== "") {
              arg += ` --duration=${this.ruleForm.duration}s`;
            } else {
              arg += ` --duration=10s`;
            }
          } else if (this.ruleForm.testType === "requests") {
            if (this.ruleForm.totalRequests !== "") {
              arg += ` --total=${this.ruleForm.totalRequests}`;
            }
          }
          if (this.customChecked) {
            if (this.ruleForm.pushUrl !== "") {
              arg += ` --push-url=${this.ruleForm.pushUrl}`;
            }
          } else if (this.prometheusChecked) {
            if (
              this.ruleForm.prometheusUrl !== "" &&
              this.ruleForm.jobName !== ""
            ) {
              arg += ` --push-url=${this.ruleForm.prometheusUrl} --prometheus-job-name=${this.ruleForm.jobName}`;
            } else {
              this.$message.error("please enter prometheus URL and job name");
              this.resetBtnStatus();
              return false;
            }
          }

          const bodyData = {
            path: this.path,
            arg: arg,
          };
          axios
            .post(apiUrl, bodyData)
            .then(res => {
              this.resetBtnStatus();
              if (res.data.code == 0) {
                this.responseContent = res.data.data;
                localStorage.setItem(
                  "PerformanceTestReport",
                  this.responseContent,
                );
                this.showResultDialogVisible = true;
                this.isShowResultBtn = true;
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.resetBtnStatus(false);
              this.$message.error(err.response.data.msg);
            });
        } else {
          return false;
        }
      });
    },

    stopPerformanceTest() {
      const apiUrl = appConfig.spongeServiceAddr + "/performanceTest/stop";
      const protocolValue = this.toLowerProtocol(this.ruleForm.protocol);
      var arg = `perftest ${protocolValue} --url=${this.ruleForm.url}`;
      if (this.ruleForm.method === "") {
        this.ruleForm.method = "GET";
      }
      arg += ` --method=${this.ruleForm.method}`;
      this.setRunPerftestBtnStatus(true, false);
      this.setStopPerftestBtnStatus(true, true);

      const bodyData = {
        path: this.path,
        arg: arg,
      };
      axios
        .post(apiUrl, bodyData)
        .then(res => {
          this.resetBtnStatus();
          if (res.data.code == 0) {
          } else {
            this.$message.error(res.data.msg);
          }
        })
        .catch(err => {
          this.resetBtnStatus();
          this.$message.error(err.response.data.msg);
        });
    },

    clearFormData() {
      this.customChecked = false;
      this.prometheusChecked = false;
      this.ruleForm = {};
      this.ruleForm.protocol = "HTTP";
      this.ruleForm.method = "GET";
    },

    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(其他)";
        this.$forceUpdate(); // 强制更新
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },
    selectChangeLLMType(val) {
      this.value = val;
      this.llmModelOptions = this.allLLMOptions[val] || [];
      if (this.llmModelOptions.length > 0) {
        this.ruleForm.llmModel = this.llmModelOptions[0].value;
      }
      this.$forceUpdate();
    },

    resetIfZero(field) {
      if (this.ruleForm[field] === 0) {
        this.ruleForm[field] = "";
      }
    },
    resetPushTypeChange() {
      this.$refs.ruleForm.clearValidate("pushUrl");
      this.$refs.ruleForm.clearValidate("prometheusUrl");
      this.$refs.ruleForm.clearValidate("jobName");
    },

    handlePushTypeChange(type, checked) {
      this.resetPushTypeChange();
      if (type === "custom") {
        this.customChecked = checked;
        if (checked) {
          this.prometheusChecked = false;
          this.ruleForm.pushType = "custom";
        } else {
          this.ruleForm.pushType = "";
        }
      } else if (type === "prometheus") {
        this.prometheusChecked = checked;
        if (checked) {
          this.customChecked = false;
          this.ruleForm.pushType = "prometheus";
        } else {
          this.ruleForm.pushType = "";
        }
      }
    },

    parseHeadersList() {
      const headersList = [];
      if (!this.ruleForm.headers) {
        return headersList;
      }

      const lines = this.ruleForm.headers.split("\n");
      for (let line of lines) {
        line = line.trim();
        if (!line) continue;

        const [key, ...rest] = line.split(":");
        if (key && rest.length > 0) {
          headersList.push({
            key: key.trim(),
            value: rest.join(":").trim(),
          });
        }
      }
      return headersList;
    },

    toLowerProtocol(proto) {
      const map = {
        HTTP: "http",
        "HTTP/2": "http2",
        "HTTP/3": "http3",
      };
      return map[proto] || proto;
    },

    toUpperProtocol(proto) {
      const map = {
        http: "HTTP",
        http2: "HTTP/2",
        http3: "HTTP/3",
      };
      return map[proto] || proto;
    },

    setRunPerftestBtnStatus(disabled, loading) {
      this.btnRunPerftestShowDisabled = disabled;
      this.btnRunPerftestLoading = loading;
    },
    setStopPerftestBtnStatus(disabled, loading) {
      this.btnStopPerftestShowDisabled = disabled;
      this.btnStopPerftestLoading = loading;
    },
    resetBtnStatus() {
      this.setRunPerftestBtnStatus(false, false);
      this.setStopPerftestBtnStatus(true, false);
    },

    showResult() {
      this.showResultDialogVisible = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.el-dialog__wrapper /deep/ .el-dialog .el-dialog__body {
  padding: 10px 50px 50px 50px;
}

.el-textarea /deep/ .el-textarea__inner {
  background: rgba(220, 220, 200, 0.3);
  border: 1px solid #ddd;
}

.title a {
  text-decoration: none;
}
.title a:hover {
  text-decoration: underline;
}
</style>



