<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <a
            href="https://go-sponge.com/zh/guide/customize/template-sql.html"
            target="_blank"
            style="color: #00BFFF"
            >基于 sql 和自定义模板生成代码</a
          >
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="数据库类型" prop="dbDriver">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.dbDriver"
              placeholder="请选择数据库类型"
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChangeDB"
            >
              <el-option
                v-for="(item, index) in dbDriverNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <!-- <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                选择数据库类型之后，生成的代码会自动匹配该数据库类型。
              </div>
            </el-tooltip> -->
          </el-form-item>

          <el-form-item label="数据库 dsn" prop="dsnAddr">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.dsnAddr"
              placeholder="user:password@(127.0.0.1:3306)/db_name"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                如果数据库是<b>mysql</b>、<b>mongodb</b>、<b>postgresql</b>，填写
                dsn 地址，<br />
                dsn 格式：<b>user:password@(host:port)/db_name</b><br />
                注意：user 和 password 不能包含特殊字符 <b>@</b> <b>:</b>
                <b>(</b> <b>)</b> <br /><br />
                mongodb 的 dsn 支持 SRV 记录，格式为：<br /><b
                  >mongodb+srv://user:password@(host:port)/db_name</b
                ><br /><br />
                postgresql 的 dsn 支持指定 schema，在 dsn 后添加参数示例：<br />
                <b>?search_path=schema1&search_path=schema2</b><br /><br />
                如果数据库是<b>sqlite</b>，填写 sqlite db 文件本地绝对路径，<br />
                示例：<b>/tmp/test.db</b> 或 <b>C:\Users\Desktop\test.db</b>
              </div>
            </el-tooltip>
            <el-link
              style="margin-left: 10px; font-size: 16px;"
              type="primary"
              @click="listTables(true)"
              >获取表名</el-link
            >
          </el-form-item>
          <el-form-item label="表名" prop="tableName">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.tableName"
              placeholder="请选择表名，支持多选"
              clearable
              filterable
              multiple
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-checkbox
                v-model="isSelectedAllTable"
                @change="selectAllTables"
                style="padding: 10px 0px 15px 20px; font-weight: bold; color: #409EFF;"
                >全选</el-checkbox
              >
              <el-badge
                v-model="ruleForm.tableName.length"
                type="primary"
                style="padding: 10px 18px 12px 0px; position: absolute; right: 0;"
              ></el-badge>
              <el-option
                v-for="(item, index) in tableNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item
            label="自定义模板目录"
            prop="templateDir"
            style="margin-top: 10px;"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.templateDir"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                自定义模板代码所在文件夹的绝对路径。<br />
                windows 环境示例：D:\tmp\template-dir<br />
                linux 环境示例：/tmp/template-dir<br /><br />
                注意：如果选择了多个表名，目录下的所有<br />
                模板文件名必须使用表名。<br />
                示例：<b>{{ tplFileNameExample }}</b>
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="自定义字段" prop="jsonFile">
            <el-upload
              class="upload-demo"
              ref="upload"
              drag
              accept=".json"
              action="#"
              :auto-upload="false"
              :on-change="handleOnchange"
              :on-remove="handleOnRemove"
              :file-list="fileList"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                <em>选择 json 文件</em>，仅限一个文件。
              </div>
            </el-upload>
          </el-form-item>
        </el-form>
        <div style="margin-bottom: 50px;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            size="default"
            @click="showTemplateInfo('ruleForm')"
            :disabled="isShowingInfo"
            :loading="isShowingInfo"
            >查看模板信息</el-button
          >
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isDownloading"
            :loading="isDownloading"
            >下载代码</el-button
          >
          <DownloadCode
            ref="downloadCode"
            @update-is-downloading="updateIsDownloading"
          ></DownloadCode>
          <ShowRepoDir ref="showRepoDir"></ShowRepoDir>
        </div>
      </div>

      <el-dialog
        top="5vh"
        title="模板信息"
        :visible.sync="showTplInfoDialogVisible"
        width="66%"
        center
      >
        <el-input
          type="textarea"
          :rows="20"
          autosize
          readonly
          v-model="tplContent"
        >
        </el-input>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
import ShowRepoDir from "@/components/ShowRepoDir";
export default {
  name: "WebModel",
  data() {
    return {
      title: "",
      path: "custom-tpl-sql",
      helpInfo: `
      <p>基于 sql、自定义的模板生成代码，更多开发细节，请参考 <a href="https://go-sponge.com/zh/guide/customize/template-sql.html" target="_blank">开发指南</a>。</p>
      `,
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isDownloading: false,
      isShowingInfo: false,

      fileList: [],
      ruleForm: {
        dsnAddr: "",
        tableName: [],
        dbDriver: "",
        templateDir: "",
        jsonFile: "",
      },
      rules: {
        dsnAddr: [
          { required: true, message: "请输入数据源 dsn", trigger: "blur" },
        ],
        tableName: [
          { required: true, message: "请选择表名", trigger: "change" },
        ],
        dbDriver: [
          { required: true, message: "请选择数据库类型", trigger: "blur" },
        ],
        templateDir: [
          {
            required: true,
            message: "请输入模板代码所在的目录绝对路径",
            trigger: "blur",
          },
        ],
      },

      showTplInfoDialogVisible: false,
      tplContent: "",
      tplFileNameExample: "{{.TableName}}.go.tmpl",

      tableNames: [],
      isSelectedAllTable: false,
      dbDriverNames: [
        { label: "mysql", value: "mysql" },
        { label: "mongodb", value: "mongodb" },
        { label: "postgresql", value: "postgresql" },
        { label: "sqlite", value: "sqlite" },
      ],
    };
  },
  created: function() {
    this.adaptiveWindows();
    this.listDbDrivers();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.dsnAddr = data.data.dsn;
          this.ruleForm.dbDriver = data.data.dbDriver;
          this.listTables(false);
          if (data.data.fields !== "") {
            this.ruleForm.jsonFile = data.data.fields;
            this.fileList.push({
              name: data.data.fields.split("/").pop(),
              raw: data.data.fields,
              status: "fromRecord",
            });
          }
          this.ruleForm.templateDir = data.data.templateDir;
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode, ShowRepoDir },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.fileList.length === 0) {
            this.downloadZipCode("");
            return;
          } else if (this.fileList.length === 1) {
            if (this.fileList[0].status === "fromRecord") {
              this.downloadZipCode(this.fileList[0].raw);
              return;
            }
          }
          // 上传文件
          let fd = new FormData();
          this.fileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = appConfig.spongeServiceAddr + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                this.downloadZipCode(res.data.data); // 下载代码
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("下载代码失败");
            });
        } else {
          return false;
        }
      });
    },

    showTemplateInfo(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.fileList.length === 0) {
            this.getTemplateInfo("");
            return;
          } else if (this.fileList.length === 1) {
            if (this.fileList[0].status === "fromRecord") {
              this.getTemplateInfo(this.fileList[0].raw);
              return;
            }
          }
          // 上传文件
          let fd = new FormData();
          this.fileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = appConfig.spongeServiceAddr + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                this.getTemplateInfo(res.data.data); // 获取模板信息
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("获取模板信息失败");
            });
        } else {
          return false;
        }
      });
    },

    downloadZipCode(jsonFile) {
      // 下载代码
      const apiUrl = appConfig.spongeServiceAddr + "/generate";
      const selectTableNames = this.ruleForm.tableName.join(",");
      const arg = `template sql --db-driver=${this.ruleForm.dbDriver} --db-dsn=${this.ruleForm.dsnAddr} --db-table=${selectTableNames} --tpl-dir=${this.ruleForm.templateDir} --fields=${jsonFile}`;
      this.$refs.downloadCode.execution(apiUrl, this.path, arg, this.helpInfo);
    },

    getTemplateInfo(jsonFile) {
      this.updateIsShowingInfo(true);
      // 查看模板信息
      const apiUrl = appConfig.spongeServiceAddr + "/getTemplateInfo";
      const selectTableNames = this.ruleForm.tableName.join(",");
      const arg = `template sql --db-driver=${this.ruleForm.dbDriver} --db-dsn=${this.ruleForm.dsnAddr} --db-table=${selectTableNames} --tpl-dir=${this.ruleForm.templateDir} --fields=${jsonFile} --only-print=true`;
      const bodyData = {
        path: this.path,
        arg: arg,
      };
      axios
        .post(apiUrl, bodyData)
        .then(res => {
          this.updateIsShowingInfo(false);
          if (res.data.code == 0) {
            this.tplContent = res.data.data;
            this.showTplInfoDialogVisible = true;
          } else {
            this.$message.error(res.data.msg);
          }
        })
        .catch(err => {
          this.updateIsShowingInfo(false);
          this.$message.error(err.response.data.msg);
        });
    },

    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    updateIsShowingInfo(value) {
      this.isShowingInfo = value;
    },
    clearFormData() {
      this.tableNames = [];
      this.fileList = [];
      this.ruleForm = {
        tableName: [],
      };
    },
    handleOnchange(file, fileList) {
      this.ruleForm.jsonFile = file.name;
      this.fileList = fileList;

      if (fileList.length !== 0) {
        this.$refs.ruleForm.validateField("jsonFile");
      }
      if (fileList.length > 1) {
        this.fileList = fileList.slice(-1);
      }
    },
    handleOnRemove(file, fileList) {
      this.fileList = fileList;
      if (fileList.length === 0) {
        this.ruleForm.jsonFile = "";
        this.$refs.ruleForm.validateField("jsonFile");
      }
    },
    listTables(isTip) {
      if (this.ruleForm.dsnAddr == "") {
        return;
      }
      const apiUrl = appConfig.spongeServiceAddr + "/listTables";
      const formdata = {
        dsn: this.ruleForm.dsnAddr,
        dbDriver: this.ruleForm.dbDriver,
      };
      axios
        .post(apiUrl, formdata, { timeout: 3000 })
        .then(res => {
          const { data } = res;
          if (isTip) {
            data.code == 0
              ? this.$message.success("获取表名成功")
              : this.$message.error("获取表名失败，" + data.msg);
          }

          this.tableNames = data.data;
        })
        .catch(err => {
          if (isTip) {
            this.$message.error(
              "获取表名失败，请检查" +
                this.ruleForm.dbDriver +
                " dsn 地址是否正确、或者" +
                this.ruleForm.dbDriver +
                "服务是否运行。",
            );
          }
          // console.log(err);
        });
    },
    listDbDrivers() {
      const apiUrl = appConfig.spongeServiceAddr + "/listDrivers";
      axios
        .get(apiUrl)
        .then(res => {
          const { data } = res;
          if (data.code == 0) {
            this.dbDriverNames = data.data;
          }
        })
        .catch(err => {
          // console.log(err);
        });
    },
    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(其他)";
        this.$forceUpdate(); // 强制更新
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      if (val.length === this.tableNames.length) {
        this.isSelectedAllTable = true;
      } else {
        this.isSelectedAllTable = false;
      }
      this.value = val;
      this.$forceUpdate();
    },
    selectAllTables(val) {
      this.ruleForm.tableName = [];
      if (val) {
        this.tableNames.forEach(item => {
          this.ruleForm.tableName.push(item.value);
        });
      }
    },

    selectChangeDB(val) {
      this.value = val;
      this.$forceUpdate();
    },

    showMutiRepoDir() {
      this.$refs.showRepoDir.isShowMutiDialog = true;
    },
    showMonoRepoDir() {
      this.$refs.showRepoDir.isShowMonoDialog = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}
.title a {
  text-decoration: none;
}
.title a:hover {
  text-decoration: underline;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.upload-demo /deep/ .el-upload-dragger {
  height: 120px;
  width: 300px;
  margin-bottom: -10px;
  margin-top: 3px;
}
.el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
.el-upload__text {
  line-height: 1.5;
  margin-bottom: 20px;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.el-dialog__wrapper /deep/ .el-dialog .el-dialog__body {
  padding: 10px 50px 30px 50px;
}

.el-textarea /deep/ .el-textarea__inner {
  background: rgba(220, 220, 200, 0.3);
  border: 1px solid #ddd;
}
</style>



