import Vue from "vue";
import Router from "vue-router";
import Home from "@/components/Home";
import DownloadCode from "@/components/DownloadCode";
import WebHTTP from "@/components/WebHTTP";
import WebHandler from "@/components/WebHandler";
import WebCache from "@/components/WebCache";
import WebDao from "@/components/WebDao";
import WebModel from "@/components/WebModel";
import MicroRPC from "@/components/MicroRPC";
import MicroRPCConn from "@/components/MicroRPCConn";
import MicroService from "@/components/MicroService";
import MicroServiceHandler from "@/components/MicroServiceHandler";
import MicroProtobuf from "@/components/MicroProtobuf";
import WebHTTPPb from "@/components/WebHTTPPb";
import MicroRPCPb from "@/components/MicroRPCPb";
import MicroGRPCHTTP from "@/components/MicroGRPCHTTP";
import MicroGRPCHTTPPb from "@/components/MicroGRPCHTTPPb";
import MicroRPCGwPb from "@/components/MicroRPCGwPb";
import YamlConfig from "@/components/YamlConfig";
import PerformanceTest from "@/components/PerformanceTest";
import CustomTplField from "@/components/CustomTplField";
import CustomTplSql from "@/components/CustomTplSql";
import CustomTplProtobuf from "@/components/CustomTplProtobuf";
import AssistantGenerate from "@/components/AssistantGenerate";
import AssistantMerge from "@/components/AssistantMerge";

import EnHome from "@/components/en/Home";
import EnDownloadCode from "@/components/en/DownloadCode";
import EnWebHTTP from "@/components/en/WebHTTP";
import EnWebHandler from "@/components/en/WebHandler";
import EnWebCache from "@/components/en/WebCache";
import EnWebDao from "@/components/en/WebDao";
import EnWebModel from "@/components/en/WebModel";
import EnMicroRPC from "@/components/en/MicroRPC";
import EnMicroRPCConn from "@/components/en/MicroRPCConn";
import EnMicroService from "@/components/en/MicroService";
import EnMicroServiceHandler from "@/components/en/MicroServiceHandler";
import EnMicroProtobuf from "@/components/en/MicroProtobuf";
import EnWebHTTPPb from "@/components/en/WebHTTPPb";
import EnMicroRPCPb from "@/components/en/MicroRPCPb";
import EnMicroGRPCHTTP from "@/components/en/MicroGRPCHTTP";
import EnMicroGRPCHTTPPb from "@/components/en/MicroGRPCHTTPPb";
import EnMicroRPCGwPb from "@/components/en/MicroRPCGwPb";
import EnYamlConfig from "@/components/en/YamlConfig";
import EnPerformanceTest from "@/components/en/PerformanceTest";
import EnCustomTplField from "@/components/en/CustomTplField";
import EnCustomTplSql from "@/components/en/CustomTplSql";
import EnCustomTplProtobuf from "@/components/en/CustomTplProtobuf";
import EnAssistantGenerate from "@/components/en/AssistantGenerate";
import EnAssistantMerge from "@/components/en/AssistantMerge";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "*",
      redirect: "/ui",
    },

    {
      path: "/ui",
      component: Home,
    },
    {
      path: "/ui/web-http",
      component: WebHTTP,
    },
    {
      path: "/ui/web-handler",
      component: WebHandler,
    },
    {
      path: "/ui/web-cache",
      component: WebCache,
    },
    {
      path: "/ui/web-dao",
      component: WebDao,
    },
    {
      path: "/ui/web-model",
      component: WebModel,
    },
    {
      path: "/ui/micro-rpc",
      component: MicroRPC,
    },
    {
      path: "/ui/micro-rpc-conn",
      component: MicroRPCConn,
    },
    {
      path: "/ui/micro-rpc-pb",
      component: MicroRPCPb,
    },
    {
      path: "/ui/micro-grpc-http",
      component: MicroGRPCHTTP,
    },
    {
      path: "/ui/micro-grpc-http-pb",
      component: MicroGRPCHTTPPb,
    },
    {
      path: "/ui/micro-rpc-gw-pb",
      component: MicroRPCGwPb,
    },
    {
      path: "/ui/micro-service",
      component: MicroService,
    },
    {
      path: "/ui/micro-service-handler",
      component: MicroServiceHandler,
    },
    {
      path: "/ui/micro-protobuf",
      component: MicroProtobuf,
    },
    {
      path: "/ui/web-http-pb",
      component: WebHTTPPb,
    },
    {
      path: "/ui/yaml-config",
      component: YamlConfig,
    },
    {
      path: "/ui/performance-test",
      component: PerformanceTest,
    },
    {
      path: "/ui/custom-tpl-field",
      component: CustomTplField,
    },
    {
      path: "/ui/custom-tpl-sql",
      component: CustomTplSql,
    },
    {
      path: "/ui/custom-tpl-protobuf",
      component: CustomTplProtobuf,
    },
    {
      path: "/ui/assistant-generate",
      component: AssistantGenerate,
    },
    {
      path: "/ui/assistant-merge",
      component: AssistantMerge,
    },
    {
      path: "/ui/download",
      component: DownloadCode,
    },

    // 添加路由之后，要在App.vue下函数adaptedRouteByLanguage添加切换英文路由
    {
      path: "/en/ui",
      component: EnHome,
    },
    {
      path: "/en/ui/web-http",
      component: EnWebHTTP,
    },
    {
      path: "/en/ui/web-handler",
      component: EnWebHandler,
    },
    {
      path: "/en/ui/web-cache",
      component: EnWebCache,
    },
    {
      path: "/en/ui/web-dao",
      component: EnWebDao,
    },
    {
      path: "/en/ui/web-model",
      component: EnWebModel,
    },
    {
      path: "/en/ui/micro-rpc",
      component: EnMicroRPC,
    },
    {
      path: "/en/ui/micro-rpc-conn",
      component: EnMicroRPCConn,
    },
    {
      path: "/en/ui/micro-rpc-pb",
      component: EnMicroRPCPb,
    },
    {
      path: "/en/ui/micro-grpc-http",
      component: EnMicroGRPCHTTP,
    },
    {
      path: "/en/ui/micro-grpc-http-pb",
      component: EnMicroGRPCHTTPPb,
    },
    {
      path: "/en/ui/micro-rpc-gw-pb",
      component: EnMicroRPCGwPb,
    },
    {
      path: "/en/ui/micro-service",
      component: EnMicroService,
    },
    {
      path: "/en/ui/micro-service-handler",
      component: EnMicroServiceHandler,
    },
    {
      path: "/en/ui/micro-protobuf",
      component: EnMicroProtobuf,
    },
    {
      path: "/en/ui/web-http-pb",
      component: EnWebHTTPPb,
    },
    {
      path: "/en/ui/yaml-config",
      component: EnYamlConfig,
    },
    {
      path: "/en/ui/performance-test",
      component: EnPerformanceTest,
    },
    {
      path: "/en/ui/custom-tpl-field",
      component: EnCustomTplField,
    },
    {
      path: "/en/ui/custom-tpl-sql",
      component: EnCustomTplSql,
    },
    {
      path: "/en/ui/custom-tpl-protobuf",
      component: EnCustomTplProtobuf,
    },
    {
      path: "/en/ui/assistant-generate",
      component: EnAssistantGenerate,
    },
    {
      path: "/en/ui/assistant-merge",
      component: EnAssistantMerge,
    },
    {
      path: "/en/ui/download",
      component: EnDownloadCode,
    },
  ],
});



