<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <a
            href="https://go-sponge.com/guide/web/based-on-protobuf.html"
            target="_blank"
            style="color: #e403a4"
            >Create web server based on protobuf</a
          >
          <span style="color: #666; font-weight:normal;">
            — generate generic web server backend code</span
          >
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="proto file" prop="protobufFile">
            <el-link
              style="margin-left: 10%;padding-bottom: 5px; font-size: 16px;"
              type="primary"
              @click="showProtoFileDemo"
              >View an example of a protobuf file</el-link
            >
            <el-upload
              class="upload-demo"
              drag
              multiple
              accept=".proto"
              action="#"
              :auto-upload="false"
              :on-change="handleOnchange"
              :on-remove="handleOnRemove"
              :file-list="fileList"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                <em>select proto file</em>, multi-file support.
              </div>
              <!-- <div class="el-upload__tip" slot="tip">
                Only proto files can be uploaded
              </div> -->
            </el-upload>
          </el-form-item>

          <el-form-item
            label="server name"
            prop="serverName"
            style="margin-top: 50px;"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.serverName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If the name contains hyphens, they will be converted to
                underscores.<br />
                example: your-server 🡆 your_server
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="module name" prop="moduleName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                module name for go.mod file.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="project name" prop="projectName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.projectName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If the name contains uppercase letters, they will be<br />
                converted to lowercase and separated by underscores.<br />
                example: yourProject 🡆 your-project
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="docker repository">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.repoAddr"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Docker image repository address, the default value is
                <b>image-repo-host</b>,<br />
                this parameter is used to build docker images, k8s deployment
                scripts, <br />
                if you use the official docker image repository, just fill in
                the user name<br />
                of the registered docker repository, if you use the private
                repository<br />
                address, you need to fill in the full repository address.<br />
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="large repository type">
            <el-switch v-model="ruleForm.suitedMonoRepo"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Disabled by default, it indicates that the generation of
                server<br />
                code suitable for a
                <b>monolithic application repository (monolith)</b><br />
                or <b>microservice multiple repository (multi-repo)</b>, where
                even if<br />
                multiple server codes are placed in one git repository, the
                codes<br />
                between different servers are not reusable.<br />
                Click to view
                <el-button type="text" @click="showMutiRepoDir"
                  >directory structure example</el-button
                >. <br /><br />
                If enabled, it indicates that the generation of server code
                suitable<br />
                for a <b>microservice monolithic repository (mono-repo)</b>,
                where<br />
                multiple server codes are placed in one git repository, and
                the<br />
                codes between different servers can be reused. If modules
                code<br />
                generated later on the page are moved to this server, this
                option<br />
                also needs to be enabled.<br />
                Click to view
                <el-button type="text" @click="showMonoRepoDir"
                  >directory structure example</el-button
                >.
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">Reset</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isDownloading"
            :loading="isDownloading"
            >Download code</el-button
          >
          <DownloadCode
            ref="downloadCode"
            @update-is-downloading="updateIsDownloading"
          ></DownloadCode>
          <ShowRepoDir ref="showRepoDir"></ShowRepoDir>
        </div>
      </div>

      <el-dialog
        top="5vh"
        title="user.proto"
        :visible.sync="showProtoDemoDialogVisible"
        width="66%"
        center
      >
        <el-input
          type="textarea"
          :rows="20"
          autosize
          readonly
          v-model="protoDemoContent"
        >
        </el-input>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/en/DownloadCode";
import ShowRepoDir from "@/components/en/ShowRepoDir";

export default {
  name: "WebHTTPPb",
  data() {
    return {
      title: "",
      path: "web-http-pb",
      helpInfo: `
      <p><b>Create web server based on protobuf</b> generates generic web server backend code, for more development details, please refer to the <a href="https://go-sponge.com/guide/web/based-on-protobuf.html" target="_blank">Development Guide</a>.</p>
      <br />
      <p>1. Unzip the code file.</p>
      <p>2. Open a terminal and execute the command to generate code: <b style="color: green">make proto</b></p>
      <p>3. Open file <span style="color: chocolate">internal/handler/xxx.go</span>, replace <span style="color: chocolate">panic("implement me")</span> according to template code example.</p>
      <p>4. Compile and run server:   <b style="color: green">make run</b></p>
      <p>5. Visit <a href="http://localhost:8080/apis/swagger/index.html" target="_blank">http://localhost:8080/apis/swagger/index.html</a> in your browser and test api.</p>
      <br />
      <p>Technology stack: <b>Go, Gin, Protobuf, Zap, Prometheus, OpenTracing</b>, etc.</p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isDownloading: false,

      fileList: [],
      ruleForm: {
        moduleName: "",
        serverName: "",
        projectName: "",
        repoAddr: "",
        protobufFile: "",
        suitedMonoRepo: false,
      },
      rules: {
        moduleName: [
          {
            required: true,
            message: "please type the name of the go module",
            trigger: "blur",
          },
        ],
        serverName: [
          {
            required: true,
            message: "please type the name of the server",
            trigger: "blur",
          },
        ],
        projectName: [
          {
            required: true,
            message: "please type the project name",
            trigger: "blur",
          },
        ],
        protobufFile: [
          {
            required: true,
            message: "please select a proto file",
            trigger: "change",
          },
        ],
      },

      showProtoDemoDialogVisible: false,
      protoDemoContent: `syntax = "proto3";

package api.user.v1;

import "google/api/annotations.proto";
import "protoc-gen-openapiv2/options/annotations.proto";

// The first user is the go module name, the second user is the server name, and v1 is the version number.
option go_package = "user/api/user/v1;v1";

option (grpc.gateway.protoc_gen_openapiv2.options.openapiv2_swagger) = {
  host: "localhost:8080"
  base_path: ""
  info: {
    title: "user api docs";
    version: "2.0";
  };
  schemes: HTTP;
  schemes: HTTPS;
  consumes: "application/json";
  produces: "application/json";
};

service user {
  // Login comments
  rpc Login(LoginRequest) returns (LoginReply) {
    option (google.api.http) = {
      post: "/api/v1/login"
      body: "*"
    };
    option (grpc.gateway.protoc_gen_openapiv2.options.openapiv2_operation) = {
      summary: "login",
      description: "login with email",
    };
  }

  // Note: The following two types of rpc will be ignored to generate http related code
  //    1. Google.api.http is not set in rpc, e.g. SayHi
  //    2. rpc is of stream type, such as ClientSteam, ServerSteam, BidiSteam

  //rpc SayHi(HelloRequest) returns (HelloReply) {}
  //rpc ClientSteam(stream HelloRequest) returns (HelloReply) {}
  //rpc ServerSteam(HelloRequest) returns (stream HelloReply) {}
  //rpc BidiSteam(stream HelloRequest) returns (stream HelloReply) {}
}

message LoginRequest {
  string email = 1;
  string password = 2;
}

message LoginReply {
  string token = 1;
}

message HelloRequest {
  string name = 1;
}

message HelloReply {
  string msg = 1;
}
`,
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          this.ruleForm.projectName = data.data.projectName;
          this.ruleForm.repoAddr = data.data.repoAddr;
          this.ruleForm.protobufFile = "";
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode, ShowRepoDir },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let fd = new FormData();
          this.fileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = appConfig.spongeServiceAddr + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                const apiUrl = appConfig.spongeServiceAddr + "/generate";
                const arg = `web http-pb --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --protobuf-file=${res.data.data}--server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --protobuf-file=${res.data.data} --suited-mono-repo=${this.ruleForm.suitedMonoRepo}`;
                this.$refs.downloadCode.execution(
                  apiUrl,
                  this.path,
                  arg,
                  this.helpInfo,
                );
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("failed to download code");
            });
        } else {
          return false;
        }
      });
    },
    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    clearFormData() {
      this.fileList = [];
      this.ruleForm = {};
    },
    handleOnchange(file, fileList) {
      this.ruleForm.protobufFile = file.name;
      this.fileList = fileList;

      if (fileList.length !== 0) {
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },
    handleOnRemove(file, fileList) {
      this.fileList = fileList;
      if (fileList.length === 0) {
        this.ruleForm.protobufFile = "";
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },

    showProtoFileDemo() {
      this.showProtoDemoDialogVisible = true;
    },

    showMutiRepoDir() {
      this.$refs.showRepoDir.isShowMutiDialog = true;
    },
    showMonoRepoDir() {
      this.$refs.showRepoDir.isShowMonoDialog = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.upload-demo /deep/ .el-upload-dragger {
  height: 60%;
  width: 150%;
}

.el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
.el-upload__text {
  line-height: 1.5;
  margin-bottom: 20px;
}

.showProtoDemoDiv {
  width: 85%;
  margin: auto;
  font-size: 16px;
  color: black;
  background: rgba(255, 255, 255, 0.5);
  line-height: 20px;
  border: 1px solid #ccc;
  padding: 5px 20px;
  margin-bottom: 20px;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.tooltip-content b {
  color: #bb6f00;
}

.title a {
  text-decoration: none;
}
.title a:hover {
  text-decoration: underline;
}

.el-dialog__wrapper /deep/ .el-dialog .el-dialog__body {
  padding: 10px 50px 30px 50px;
}

.el-textarea /deep/ .el-textarea__inner {
  background: rgba(220, 220, 200, 0.3);
  border: 1px solid #ddd;
}
</style>



