<template>
  <div id="app">
    <div>
      <el-container>
        <el-header style="height: 100px; border-bottom: 2px solid #eee">
          <div
            class="header-image"
            :style="{ 'padding-left': logoImagePosition }"
          >
            <el-image style="height: 50px;" :src="logoImageSrc"></el-image>
          </div>
          <div
            class="menuControl"
            @mouseenter="changebgcolor"
            @mouseleave="clearcolor"
            @click="collapseStatus"
            :style="{ 'background-color': collapsebgcolor }"
          >
            <div style="text-align: center;padding-top: 5px;">
              <i class="el-icon-s-fold" v-show="block"></i>
              <i class="el-icon-s-unfold" v-show="toggle"></i>
            </div>
          </div>
          <div style="float:right;margin-top: -20px;">
            <el-select
              class="languageClass"
              v-model="languageValue"
              @change="changeLanguage"
            >
              <el-option
                style="font-size: medium;"
                v-for="item in languageOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-header>
        <el-container>
          <el-aside class="aside-menu" :width="collapseSize">
            <el-menu
              router
              default-active="$router.currentRoute.path"
              :default-active="currentMenu"
              class="el-menu-vertical-demo"
              @select="handleSelect"
              @open="handleOpen"
              @close="handleClose"
              :collapse="isCollapse"
            >
              <el-menu-item :index="homeRoute">
                <i class="el-icon-s-home"></i>
                <span class="home-class" slot="title">Home</span>
              </el-menu-item>

              <el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-coin"></i>
                  <span class="el-menu-span1" slot="title">SQL</span>
                </template>
                <el-menu-item :index="webHttpRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.webHttp") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="microRPCRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.microRpc") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="microGRPCHTTPRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.microGRPCHTTP") }}
                  </span>
                </el-menu-item>
              </el-submenu>

              <el-submenu index="2">
                <template slot="title">
                  <i class="el-icon-tickets"></i>
                  <span class="el-menu-span1" slot="title">Protobuf</span>
                </template>
                <el-menu-item :index="webHTTPPbRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.webHttpPb") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="microRPCPbRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.microrpcPb") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="microGRPCHTTPPbRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.microGRPCHTTPPb") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="microRPCGwPbRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.microRpcGwPb") }}
                  </span>
                </el-menu-item>
              </el-submenu>

              <el-submenu index="3">
                <template slot="title">
                  <i class="el-icon-suitcase"></i>
                  <span class="el-menu-span1" slot="title">Public</span>
                </template>
                <el-menu-item :index="webHandlerRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.wehHandler") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="microServiceRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.microService") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="microServiceHandlerRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.microServiceHandler") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="webDaoRoute">
                  <span class="el-menu-span3">{{ $t("navMenu.webDao") }} </span>
                </el-menu-item>
                <el-menu-item :index="microProtobufRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.microProtobuf") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="webModelRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.webModel") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="yamlConfigRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.yamlConfig") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="microRPCConnRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.microRpcConn") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="webCacheRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.webCache") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="performanceTestRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.performanceTest") }}
                  </span>
                </el-menu-item>
              </el-submenu>

              <el-submenu index="4">
                <template slot="title">
                  <i class="el-icon-edit-outline"></i>
                  <span class="el-menu-span1" slot="title">{{
                    $t("navMenu.customType")
                  }}</span>
                </template>
                <el-menu-item :index="customTplFieldRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.customTplField") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="customTplSqlRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.customTplSql") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="customTplProtobufRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.customTplProtobuf") }}
                  </span>
                </el-menu-item>
              </el-submenu>

              <el-submenu index="5">
                <template slot="title">
                  <i class="el-icon-document"></i>
                  <span class="el-menu-span1" slot="title">{{
                    $t("navMenu.assistant")
                  }}</span>
                </template>
                <el-menu-item :index="assistantGenerate">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.assistantGenerate") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="assistantMerge">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.assistantMerge") }}
                  </span>
                </el-menu-item>
              </el-submenu>
            </el-menu>
          </el-aside>

          <el-container>
            <el-main>
              <!-- 路由 -->
              <router-view />
            </el-main>
            <el-footer>
              <div style="text-align: center; position:relative;">
                <p style="font-style:italic; font-size: medium;color: #666; ">
                  {{ $t("footer.text1") }}
                  <a
                    href="https://github.com/go-dev-frame/sponge"
                    target="_blank"
                  >
                    star⭐</a
                  >
                  {{ $t("footer.text2") }}
                  <el-button
                    type="text"
                    style="font-size: medium;"
                    @click="dialogTableVisible = true"
                    >{{ $t("footer.text3") }}</el-button
                  >
                </p>

                <el-dialog
                  title="加微信进群(标注sponge)"
                  :visible.sync="dialogTableVisible"
                >
                  <el-image
                    style="height: 320px;"
                    :src="wechatImageSrc"
                  ></el-image>
                </el-dialog>
              </div>
            </el-footer>
          </el-container>
        </el-container>
      </el-container>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      isCollapse: false,
      // collapseBtnClick: false,
      collapseSize: "320px",
      toggle: false,
      block: true,
      collapsebgcolor: "",

      dialogTableVisible: false,
      logoImageSrc: require("@/assets/go-sponge-logo.png"),
      logoImagePosition: "18%",
      wechatImageSrc: require("@/assets/wechat.jpg"),
      currentMenu: "/ui",

      languageOptions: [
        {
          value: "🇺🇸 English",
          label: "🇺🇸 English",
        },
        {
          value: "🇨🇳 中文",
          label: "🇨🇳 中文",
        },
      ],
      languageValue: "🇨🇳 中文",

      homeRoute: "/ui",
      webHttpRoute: "/ui/web-http",
      webHandlerRoute: "/ui/web-handler",
      webCacheRoute: "/ui/web-cache",
      webDaoRoute: "/ui/web-dao",
      webModelRoute: "/ui/web-model",
      microRPCRoute: "/ui/micro-rpc",
      microServiceRoute: "/ui/micro-service",
      microServiceHandlerRoute: "/ui/micro-service-handler",
      microRPCConnRoute: "/ui/micro-rpc-conn",
      microRPCPbRoute: "/ui/micro-rpc-pb",
      microGRPCHTTPRoute: "/ui/micro-grpc-http",
      microGRPCHTTPPbRoute: "/ui/micro-grpc-http-pb",
      microRPCGwPbRoute: "/ui/micro-rpc-gw-pb",
      microProtobufRoute: "/ui/micro-protobuf",
      webHTTPPbRoute: "/ui/web-http-pb",
      yamlConfigRoute: "/ui/yaml-config",
      performanceTestRoute: "/ui/performance-test",
      customTplFieldRoute: "/ui/custom-tpl-field",
      customTplSqlRoute: "/ui/custom-tpl-sql",
      customTplProtobufRoute: "/ui/custom-tpl-protobuf",
      assistantGenerate: "/ui/assistant-generate",
      assistantMerge: "/ui/assistant-merge",
      downloadCodeRoute: "/ui/download",
    };
  },

  // 刷新展开折叠菜单
  watch: {
    $route(e) {
      this.currentMenu = e.path;
    },
  },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  created() {
    let localeLanguage = localStorage.getItem("localeLanguage");
    if (localeLanguage == "en") {
      this.$i18n.locale = "en";
      this.languageValue = "🇺🇸 English";
    } else {
      this.$i18n.locale = "cn";
      this.languageValue = "🇨🇳 中文";
    }
    this.adaptedRouteByLanguage();

    this.currentMenu = this.$route.path;
    if (this.$i18n.locale == "en" && this.currentMenu == "/ui") {
      this.$router.push(this.homeRoute);
    }

    window.addEventListener("resize", this.handleResize);
  },

  beforeDestroy: function() {
    window.removeEventListener("resize", this.handleResize);
  },

  methods: {
    handleResize(event) {
      this.fullWidth = document.documentElement.clientWidth;
    },
    handleSelect(key, keyPath) {
      // this.currentMenu = key;
    },
    handleOpen(key, keyPath) {
      // this.currentMenu = key;
      // console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      // this.currentMenu = key;
      // console.log(key, keyPath);
    },
    clickHandle(sub) {
      // console.log(sub.id, sub.name);
    },

    collapseStatus() {
      // this.collapseBtnClick = this.isCollapse;
      this.toggle = !this.toggle;
      this.block = !this.block;
      this.isCollapse = !this.isCollapse;
      if (this.isCollapse) {
        this.collapseSize = "0px";
        this.logoImagePosition = "0%";
      } else {
        this.collapseSize = "320px";
        this.logoImagePosition = "18%";
      }
    },

    // 鼠标移动上更换背景色
    changebgcolor() {
      // index:鼠标移上的元素的索引值
      this.collapsebgcolor = "#66B1FF";
    },
    //鼠标移出，还原颜色
    clearcolor() {
      this.collapsebgcolor = "";
    },

    changeLanguage(value) {
      this.languageValue = value;
      if (value === "🇨🇳 中文") {
        this.$i18n.locale = "cn";
        localStorage.setItem("localeLanguage", "cn");
      } else {
        this.$i18n.locale = "en";
        localStorage.setItem("localeLanguage", "en");
      }
      this.adaptedRouteByLanguage();

      this.$router.push(this.homeRoute);
      window.location.reload();
    },

    adaptedRouteByLanguage() {
      var enPrefix = "";
      if (this.$i18n.locale == "en") {
        enPrefix = "/en";
      }

      this.homeRoute = enPrefix + this.homeRoute.replace("/en", "");
      this.webHttpRoute = enPrefix + this.webHttpRoute.replace("/en", "");
      this.webHandlerRoute = enPrefix + this.webHandlerRoute.replace("/en", "");
      this.webCacheRoute = enPrefix + this.webCacheRoute.replace("/en", "");
      this.webDaoRoute = enPrefix + this.webDaoRoute.replace("/en", "");
      this.webModelRoute = enPrefix + this.webModelRoute.replace("/en", "");
      this.microRPCRoute = enPrefix + this.microRPCRoute.replace("/en", "");
      this.microRPCConnRoute =
        enPrefix + this.microRPCConnRoute.replace("/en", "");
      this.microRPCPbRoute = enPrefix + this.microRPCPbRoute.replace("/en", "");
      this.microGRPCHTTPRoute =
        enPrefix + this.microGRPCHTTPRoute.replace("/en", "");
      this.microGRPCHTTPPbRoute =
        enPrefix + this.microGRPCHTTPPbRoute.replace("/en", "");
      this.microRPCGwPbRoute =
        enPrefix + this.microRPCGwPbRoute.replace("/en", "");
      this.microServiceRoute =
        enPrefix + this.microServiceRoute.replace("/en", "");
      this.microServiceHandlerRoute =
        enPrefix + this.microServiceHandlerRoute.replace("/en", "");
      this.microProtobufRoute =
        enPrefix + this.microProtobufRoute.replace("/en", "");
      this.webHTTPPbRoute = enPrefix + this.webHTTPPbRoute.replace("/en", "");
      this.yamlConfigRoute = enPrefix + this.yamlConfigRoute.replace("/en", "");
      this.performanceTestRoute =
        enPrefix + this.performanceTestRoute.replace("/en", "");
      this.customTplFieldRoute =
        enPrefix + this.customTplFieldRoute.replace("/en", "");
      this.customTplSqlRoute =
        enPrefix + this.customTplSqlRoute.replace("/en", "");
      this.customTplProtobufRoute =
        enPrefix + this.customTplProtobufRoute.replace("/en", "");
      this.assistantGenerate =
        enPrefix + this.assistantGenerate.replace("/en", "");
      this.assistantMerge = enPrefix + this.assistantMerge.replace("/en", "");
      this.downloadCodeRoute =
        enPrefix + this.downloadCodeRoute.replace("/en", "");
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
        this.logoImagePosition = "0%";
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
        this.logoImagePosition = "18%";
      }
    },
  },
};
</script>

<style scoped>
#app {
  margin: 0px;
  padding: 0px;
  background: url("assets/body-background.png") repeat;
}

.menuControl {
  cursor: pointer;
  float: left;
  width: 50px;
  height: 30px;
  border-radius: 5%;
  margin-top: -15px;
  margin-left: -10px;
}

.aside-menu {
  margin: 0px;
  padding: 0px;
  background-color: #f8f8f8;
  overflow-y: hidden;
  /* overflow-x: hidden; */
}

.aside-menu /deep/ .el-menu {
  background-color: #f8f8f8;
}

.el-menu-vertical-demo:not(.el-menu--collapse) {
  /* width: 300px; */
  min-height: 800px;
}
.el-submenu /deep/ .el-menu-item {
  font-family: "Microsoft YaHei UI";
  padding: 0 15px;
  height: 40px;
  line-height: 40px;
}

.el-col {
  /* border-radius: 4px; */
  background: #99a9bf;
  background: url("assets/body-background.png");
}

.home-class {
  font-weight: bold;
  font-size: 15px;
}

.el-menu-span1 {
  font-family: "Microsoft YaHei UI";
  font-weight: bold;
  font-size: 15px;
}
.el-menu-span2 {
  font-family: "Microsoft YaHei UI";
  font-weight: bold;
  padding: 0 15px;
  font-size: 14px;
}
.el-menu-span3 {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  padding: 0 15px;
}

.header-image {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  padding-top: 25px;
  padding-left: 18%;
}

.languageClass /deep/ .el-input__inner {
  width: 120px;
  font-size: medium;
  background-color: transparent;
  color: #409eff;
  border: none;
}
</style>



