<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3 style="color: #484848;">{{ title }}</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="module 名称" prop="moduleName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.moduleName"
              placeholder="请输入 module 名称"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                例如生成的代码将要移动到<b>user</b>服务代码目录，<br />
                这里填写<b>user</b>服务对应 go.mod 文件的 module 名称。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item
            label="服务名称"
            prop="serverName"
            v-if="ruleForm.suitedMonoRepo"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.serverName"
              placeholder="必填字段"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                例如生成的代码将要移动到<b>user</b>服务代码目录，则这里填写<b>user</b>。<br />
                如果名称包括中划线"-"，会转换为下划线"_"，<br />
                示例：your-server 🡆 your_server
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="缓存名称" prop="cacheName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.cacheName"
              placeholder="示例 userToken"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="缓存 key 前缀"
            prop="prefixKey"
            v-if="ruleForm.isNeedCustomKeyPrefix"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.prefixKey"
              placeholder="示例 user:token"
            ></el-input>
          </el-form-item>
          <el-form-item label="key 名称" prop="keyName">
            <el-input
              style="width: 40%; font-size: medium"
              v-model.trim="ruleForm.keyName"
              placeholder="示例 id"
            ></el-input>
            <el-select
              style="width: 40%; font-size: medium"
              v-model="ruleForm.keyType"
              placeholder="key 的 go 类型"
              filterable
              allow-create
              clearable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in keyTypes"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item label="value 名称" prop="valueName">
            <el-input
              style="width: 40%; font-size: medium"
              v-model.trim="ruleForm.valueName"
              placeholder="示例 token"
            ></el-input>
            <el-select
              style="width: 40%; font-size: medium"
              v-model="ruleForm.valueType"
              placeholder="value 的 go 类型"
              filterable
              allow-create
              clearable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in valueTypes"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item label="自定义缓存 key 前缀">
            <el-switch v-model="ruleForm.isNeedCustomKeyPrefix"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                在缓存服务中存储的 key 名称默认是
                <b>缓存名称:key 值</b>。<br /><br />
                如果开启此选项，设置自定义的缓存 key 前缀，<br />key 名称将变为
                <b>自定义缓存 key 前缀:key 值</b>。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="大仓库类型">
            <el-switch v-model="ruleForm.suitedMonoRepo"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                默认关闭，表示生成的模块代码只适合<b>单体应用单仓库(monolith)</b>或<br />
                <b>微服务多仓库(multi-repo)</b>类型的项目代码。<br /><br />
                如果开启，表示生成的模块代码只适合<b>微服务单仓库(mono-repo)</b
                ><br />类型的项目代码。
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isDownloading"
            :loading="isDownloading"
            >下载代码</el-button
          >
          <DownloadCode
            ref="downloadCode"
            @update-is-downloading="updateIsDownloading"
          ></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "WebCache",
  data() {
    return {
      title: "生成 cache 代码",
      path: "web-cache",
      helpInfo: `
      <p>生成 Key-Value 缓存(redis)代码, 更多开发细节，请参考 <a href="https://go-sponge.com/guide/module-code/gen-cache.html" target="_blank">Development Guide</a>。</p>
      <br />
      <p>解压文件，将文件夹<b>internal</b>移动到项目代码文件夹下。</p>
      `,
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isDownloading: false,

      ruleForm: {
        moduleName: "",
        serverName: "",
        cacheName: "",
        prefixKey: "",
        keyName: "",
        keyType: "",
        valueName: "",
        valueType: "",
        suitedMonoRepo: false,
        isNeedCustomKeyPrefix: false,
      },
      rules: {
        moduleName: [
          { required: true, message: "请输入 go module 名称", trigger: "blur" },
        ],
        cacheName: [
          { required: true, message: "请输入缓存名称", trigger: "blur" },
        ],
        // prefixKey: [
        //   { required: true, message: "请输入缓存 key 前缀名称", trigger: "blur" },
        // ],
        keyName: [
          { required: true, message: "请输入 key 名称", trigger: "blur" },
        ],
        keyType: [
          { required: true, message: "请选择 key 的 go 类型", trigger: "blur" },
        ],
        valueName: [
          { required: true, message: "请输入 value 名称", trigger: "blur" },
        ],
        valueType: [
          { required: true, message: "请选择 value 的 go 类型", trigger: "blur" },
        ],
      },
      keyTypes: [
        { label: "uint64", value: "uint64" },
        { label: "string", value: "string" },
        { label: "int", value: "int" },
        { label: "int64", value: "int64" },
      ],
      valueTypes: [
        { label: "uint64", value: "uint64" },
        { label: "string", value: "string" },
        { label: "int", value: "int" },
        { label: "int64", value: "int64" },
      ],
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          var arg = "";
          if (this.ruleForm.keyType == "") {
            this.$message.error("key 的 go 类型不能为空");
            return false;
          }
          if (this.ruleForm.valueType == "") {
            this.$message.error("value 的 go 类型不能为空");
            return false;
          }
          if (this.ruleForm.suitedMonoRepo) {
            if (this.ruleForm.serverName == "") {
              this.$message.error("服务名称不能为空");
              return false;
            }
            arg = `web cache --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --cache-name=${this.ruleForm.cacheName} --prefix-key=${this.ruleForm.prefixKey} --key-name=${this.ruleForm.keyName} --key-type=${this.ruleForm.keyType} --value-name=${this.ruleForm.valueName} --value-type=${this.ruleForm.valueType} --suited-mono-repo=${this.ruleForm.suitedMonoRepo}`;
          } else {
            arg = `web cache --module-name=${this.ruleForm.moduleName} --cache-name=${this.ruleForm.cacheName} --prefix-key=${this.ruleForm.prefixKey} --key-name=${this.ruleForm.keyName} --key-type=${this.ruleForm.keyType} --value-name=${this.ruleForm.valueName} --value-type=${this.ruleForm.valueType} --suited-mono-repo=${this.ruleForm.suitedMonoRepo}`;
          }
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    clearFormData() {
      this.ruleForm = {};
    },

    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(其他)";
        this.$forceUpdate();
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.tooltip-content b {
  color: #bb6f00;
}
</style>



