<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <a
            href="https://go-sponge.com/guide/ai/assistant.html#usage-example"
            target="_blank"
            style="color: #00AEB1;"
            >{{ title }}</a
          >
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="AI Assistant Type" prop="llmType">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.llmType"
              placeholder="please select AI assistant type"
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChangeLLMType"
            >
              <el-option
                v-for="(item, index) in llmTypeOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>

          <el-form-item label="Model Name" prop="llmModel">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.llmModel"
              placeholder="please select or input model name"
              filterable
              allow-create
              clearable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in llmModelOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                supported manual input or select existing model name.
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="API Key" prop="apiKey">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.apiKey"
              :type="showPassword ? 'text' : 'password'"
              placeholder="please enter API Key from AI service provider"
            >
              <template #append>
                <el-button
                  :icon="showPassword ? 'el-icon-view' : 'el-icon-view'"
                  @click="togglePassword"
                />
              </template>
            </el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Fill in the API Key of the AI assistant<br />
                to call the corresponding service.
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="Code Range" prop="targetType">
            <el-radio-group
              v-model="ruleForm.targetType"
              @change="handleTargetTypeChange"
            >
              <el-radio label="goDir">Go Project</el-radio>
              <el-radio label="goFile">Go File</el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item
            label="Go Project"
            prop="goDir"
            style="margin-top: 10px;"
            v-if="ruleForm.targetType === 'goDir'"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.goDir"
              placeholder="please enter the absolute path of the local Go project code"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If the project is created by sponge, the AI assistant will<br />
                automatically generate the corresponding code based on<br />
                the project structure and context.<br />
                Windows example: D:\project\dir\internal\service<br />
                Linux example: /project/dir/internal/service<br />
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item
            label="Go File"
            prop="goFile"
            v-if="ruleForm.targetType === 'goFile'"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.goFile"
              placeholder="please enter the absolute path of the local Go file"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If the file is a Go file in a sponge project, and the file is
                located<br />
                in the <b>handler</b>, <b>service</b>, and other directories,
                the AI assistant will<br />
                automatically generate the corresponding code based on the<br />
                project structure and context.<br />
                Windows example: D:\project\dir\internal\service\file.go<br />
                Linux example: /project/dir/internal/service/file.go<br />
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">Reset</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isShowingInfo"
            :loading="isShowingInfo"
            >Generate Code</el-button
          >
          <el-button size="default" v-show="isShowResultBtn" @click="showResult"
            >View Results</el-button
          >
        </div>
      </div>

      <el-dialog
        top="5vh"
        title="Execution Result"
        :visible.sync="showResultDialogVisible"
        width="66%"
        center
      >
        <el-input
          type="textarea"
          :rows="20"
          autosize
          readonly
          v-model="responseContent"
        >
        </el-input>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
export default {
  name: "AssistantGenerate",
  data() {
    return {
      title:
        "AI Assistant Automatically Generates Business Logic Code by Understanding Project Context",
      path: "assistant-generate",
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isDownloading: false,
      showPassword: false,

      isShowingInfo: false,
      responseContent: "",
      showResultDialogVisible: false,
      isShowResultBtn: false,

      ruleForm: {
        llmType: "",
        llmModel: "",
        apiKey: "",
        targetType: "goDir",
        goDir: "",
        goFile: "",
      },
      rules: {
        llmType: [
          {
            required: true,
            message: "please select AI assistant type",
            trigger: "blur",
          },
        ],
        llmModel: [
          {
            required: true,
            message: "please select or input model name",
            trigger: "blur",
          },
        ],
        apiKey: [
          { required: true, message: "please enter API Key", trigger: "blur" },
        ],
        goDir: [
          {
            required: true,
            message: "please enter Go project path",
            trigger: "blur",
          },
        ],
        goFile: [
          {
            required: true,
            message: "please enter Go file path",
            trigger: "blur",
          },
        ],
      },

      llmTypeOptions: [
        { label: "deepseek", value: "deepseek" },
        { label: "chatgpt", value: "chatgpt" },
        { label: "gemini", value: "gemini" },
      ],
      llmModelOptions: [],
      allLLMOptions: {
        deepseek: [
          { label: "deepseek-chat", value: "deepseek-chat" },
          { label: "deepseek-reasoner", value: "deepseek-reasoner" },
        ],
        chatgpt: [
          { label: "gpt-5", value: "gpt-5" },
          { label: "gpt-5-thinking", value: "gpt-5-thinking" },
          { label: "gpt-4.1", value: "gpt-4.1" },
          { label: "gpt-4.1-mini", value: "gpt-4.1-mini" },
          { label: "gpt-4o", value: "gpt-4o" },
          { label: "gpt-4o-mini", value: "gpt-4o-mini" },
        ],
        gemini: [
          { label: "gemini-2.5-flash", value: "gemini-2.5-flash" },
          { label: "gemini-2.5-pro", value: "gemini-2.5-pro" },
          { label: "gemini-2.5-flash-lite", value: "gemini-2.5-flash-lite" },
          { label: "gemini-2.0-flash", value: "gemini-2.0-flash" },
          { label: "gemini-2.0-pro", value: "gemini-2.0-pro" },
        ],
      },
    };
  },

  created: function() {
    this.adaptiveWindows();
    this.listLLM();

    this.responseContent = localStorage.getItem(
      "AIAssistantGenerateCodeReport",
    );
    if (this.responseContent !== null && this.responseContent.length > 0) {
      this.isShowResultBtn = true;
    }

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.llmType = data.data.llmType;
          this.ruleForm.llmModel = data.data.llmModel;
          this.ruleForm.apiKey = data.data.apiKey;
          this.ruleForm.goDir = data.data.goDir;
          this.ruleForm.goFile = data.data.goFile;
          if (data.data.targetType !== "") {
            this.ruleForm.targetType = data.data.targetType;
          }
          this.selectChangeLLMType(this.ruleForm.llmType);
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.updateIsShowingInfo(true);
          const apiUrl = appConfig.spongeServiceAddr + "/assistant";
          var arg = "";
          if (this.ruleForm.targetType !== "goDir") {
            arg = `assistant generate --type=${this.ruleForm.llmType} --model=${this.ruleForm.llmModel} --api-key=${this.ruleForm.apiKey} --file=${this.ruleForm.goFile}`;
          } else {
            arg = `assistant generate --type=${this.ruleForm.llmType} --model=${this.ruleForm.llmModel} --api-key=${this.ruleForm.apiKey} --dir=${this.ruleForm.goDir}`;
          }
          const bodyData = {
            path: this.path,
            arg: arg,
          };
          axios
            .post(apiUrl, bodyData)
            .then(res => {
              this.updateIsShowingInfo(false);
              if (res.data.code == 0) {
                this.responseContent = res.data.data;
                localStorage.setItem(
                  "AIAssistantGenerateCodeReport",
                  this.responseContent,
                );
                this.showResultDialogVisible = true;
                this.isShowResultBtn = true;
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.updateIsShowingInfo(false);
              this.$message.error(err.response.data.msg);
            });
        } else {
          return false;
        }
      });
    },
    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    clearFormData() {
      this.ruleForm.llmType = "chatgpt";
      this.ruleForm.targetType = "goDir";
      this.ruleForm.goDir = "";
      this.ruleForm.goFile = "";
      this.ruleForm.apiKey = "";
      this.selectChangeLLMType(this.ruleForm.llmType);
    },

    listLLM() {
      const apiUrl = appConfig.spongeServiceAddr + "/listLLM";
      axios
        .get(apiUrl)
        .then(res => {
          const { data } = res;
          if (data.code == 0) {
            this.llmTypeOptions = data.data.llmTypeOptions;
            this.allLLMOptions = data.data.allLLMOptions;
          }
        })
        .catch(err => {
          // console.log(err);
        });
    },

    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(其他)";
        this.$forceUpdate(); // 强制更新
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },
    selectChangeLLMType(val) {
      this.value = val;
      this.llmModelOptions = this.allLLMOptions[val] || [];
      if (this.llmModelOptions.length > 0) {
        this.ruleForm.llmModel = this.llmModelOptions[0].value;
      }
      this.$forceUpdate();
    },

    handleTargetTypeChange(val) {
      if (val === "goDir") {
        this.$refs.ruleForm.clearValidate("goFile");
        this.$refs.ruleForm.clearValidate("goDir");
      } else if (val === "goFile") {
        this.$refs.ruleForm.clearValidate("goDir");
        this.$refs.ruleForm.clearValidate("goFile");
      }
    },

    togglePassword() {
      this.showPassword = !this.showPassword;
    },

    updateIsShowingInfo(value) {
      this.isShowingInfo = value;
    },

    showResult() {
      this.showResultDialogVisible = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.el-dialog__wrapper /deep/ .el-dialog .el-dialog__body {
  padding: 10px 50px 50px 50px;
}

.el-textarea /deep/ .el-textarea__inner {
  background: rgba(220, 220, 200, 0.3);
  border: 1px solid #ddd;
}

.title a {
  text-decoration: none;
}
.title a:hover {
  text-decoration: underline;
}
</style>



