<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <a
            href="https://go-sponge.com/guide/ai/assistant.html#merge-code"
            target="_blank"
            style="color: #4169E1;"
            >{{ title }}</a
          >
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="AI Assistant Type" prop="llmType">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.llmType"
              placeholder="please select AI assistant type"
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChangeLLMType"
            >
              <el-option
                v-for="(item, index) in llmTypeOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Select the same type of AI assistant that<br />
                was used to generate the code.
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="Code Range" prop="targetType">
            <el-radio-group
              v-model="ruleForm.targetType"
              @change="handleTargetTypeChange"
            >
              <el-radio label="goDir">Go Project</el-radio>
              <el-radio label="goFile">Go File</el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item
            label="Go Project"
            prop="goDir"
            style="margin-top: 10px;"
            v-if="ruleForm.targetType === 'goDir'"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.goDir"
              placeholder="Enter the absolute path of the local Go project"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                The Go project path is the same as the<br />
                code generated by the AI assistant.<br />
                Windows example: D:\project\dir\internal\service<br />
                Linux example: /project/dir/internal/service<br />
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item
            label="Go File"
            prop="goFile"
            v-if="ruleForm.targetType === 'goFile'"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.goFile"
              placeholder="Enter the absolute path of the local Go file"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Same Go file path as the code generated<br />
                by the AI assistant.<br />
                Windows example: D:\project\dir\internal\service\file.go<br />
                Linux example: /project/dir/internal/service/file.go<br />
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="Clear AI Code">
            <el-switch v-model="ruleForm.isClean"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                After merging the code, clear the code<br />
                generated by the AI assistant.
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">Reset</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isShowingInfo"
            :loading="isShowingInfo"
            >Merge Code</el-button
          >
          <el-button size="default" v-show="isShowResultBtn" @click="showResult"
            >View Result</el-button
          >
        </div>
      </div>

      <el-dialog
        top="5vh"
        title="Results of execution"
        :visible.sync="showResultDialogVisible"
        width="66%"
        center
      >
        <el-input
          type="textarea"
          :rows="20"
          autosize
          readonly
          v-model="responseContent"
        >
        </el-input>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
export default {
  name: "AssistantMerge",
  data() {
    return {
      title:
        "Merge code generated by AI assistant into corresponding Go code file",
      path: "assistant-merge",
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,

      isShowingInfo: false,
      responseContent: "",
      showResultDialogVisible: false,
      isShowResultBtn: false,

      ruleForm: {
        llmType: "",
        targetType: "goDir",
        goDir: "",
        goFile: "",
        isClean: false,
      },
      rules: {
        llmType: [
          {
            required: true,
            message: "please select the AI assistant type",
            trigger: "blur",
          },
        ],
        goDir: [
          {
            required: true,
            message: "please enter the Go project path",
            trigger: "blur",
          },
        ],
        goFile: [
          {
            required: true,
            message: "please enter the Go file path",
            trigger: "blur",
          },
        ],
      },

      llmTypeOptions: [
        { label: "deepseek", value: "deepseek" },
        { label: "chatgpt", value: "chatgpt" },
        { label: "gemini", value: "gemini" },
      ],
    };
  },

  created: function() {
    this.adaptiveWindows();
    this.listLLM();

    this.responseContent = localStorage.getItem("AIAssistantMergeCodeReport");
    if (this.responseContent !== null && this.responseContent.length > 0) {
      this.isShowResultBtn = true;
    }

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.llmType = data.data.llmType;
          this.ruleForm.goDir = data.data.goDir;
          this.ruleForm.goFile = data.data.goFile;
          this.ruleForm.targetType = data.data.targetType;
          this.ruleForm.isClean = data.data.isCleanAICode;
        }
      })
      .catch(err => {
        // console.log(err);
      });

    if (this.ruleForm.targetType === "") {
      this.ruleForm.targetType = "goDir";
    }
  },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.updateIsShowingInfo(true);
          const apiUrl = appConfig.spongeServiceAddr + "/assistant";
          var arg = "";
          if (this.ruleForm.targetType !== "goDir") {
            arg = `assistant merge --type=${this.ruleForm.llmType} --file=${this.ruleForm.goFile} --is-clean=${this.ruleForm.isClean}`;
          } else {
            arg = `assistant merge --type=${this.ruleForm.llmType} --dir=${this.ruleForm.goDir} --is-clean=${this.ruleForm.isClean}`;
          }
          const bodyData = {
            path: this.path,
            arg: arg,
          };
          axios
            .post(apiUrl, bodyData)
            .then(res => {
              this.updateIsShowingInfo(false);
              if (res.data.code == 0) {
                this.responseContent = res.data.data;
                localStorage.setItem(
                  "AIAssistantMergeCodeReport",
                  this.responseContent,
                );
                this.showResultDialogVisible = true;
                this.isShowResultBtn = true;
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.updateIsShowingInfo(false);
              this.$message.error(err.response.data.msg);
            });
        } else {
          return false;
        }
      });
    },

    clearFormData() {
      this.ruleForm = {};
      this.ruleForm.targetType = "goDir";
      this.ruleForm.isClean = false;
    },

    listLLM() {
      const apiUrl = appConfig.spongeServiceAddr + "/listLLM";
      axios
        .get(apiUrl)
        .then(res => {
          const { data } = res;
          if (data.code == 0) {
            this.llmTypeOptions = data.data.llmTypeOptions;
            this.allLLMOptions = data.data.allLLMOptions;
          }
        })
        .catch(err => {
          // console.log(err);
        });
    },

    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(其他)";
        this.$forceUpdate(); // 强制更新
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },
    selectChangeLLMType(val) {
      this.value = val;
      this.$forceUpdate();
    },

    handleTargetTypeChange(val) {
      if (val === "goDir") {
        this.$refs.ruleForm.clearValidate("goFile");
        this.$refs.ruleForm.clearValidate("goDir");
      } else if (val === "goFile") {
        this.$refs.ruleForm.clearValidate("goDir");
        this.$refs.ruleForm.clearValidate("goFile");
      }
    },

    updateIsShowingInfo(value) {
      this.isShowingInfo = value;
    },

    showResult() {
      this.showResultDialogVisible = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.el-dialog__wrapper /deep/ .el-dialog .el-dialog__body {
  padding: 10px 50px 50px 50px;
}

.el-textarea /deep/ .el-textarea__inner {
  background: rgba(220, 220, 200, 0.3);
  border: 1px solid #ddd;
}

.title a {
  text-decoration: none;
}
.title a:hover {
  text-decoration: underline;
}
</style>



