<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3 style="color: #484848;">{{ title }}</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="数据库类型" prop="dbDriver">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.dbDriver"
              placeholder="请选择数据库类型"
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChangeDB"
            >
              <el-option
                v-for="(item, index) in dbDriverNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                选择数据库类型之后，生成的代码会自动匹配该数据库类型。
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="数据库 dsn" prop="dsnAddr">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.dsnAddr"
              placeholder="user:password@(127.0.0.1:3306)/db_name"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                如果数据库是<b>mysql</b>、<b>mongodb</b>、<b>postgresql</b>，填写
                dsn 地址，<br />
                dsn 格式：<b>user:password@(host:port)/db_name</b><br />
                注意：user 和 password 不能包含特殊字符 <b>@</b> <b>:</b>
                <b>(</b> <b>)</b> <br /><br />
                mongodb 的 dsn 支持 SRV 记录，格式为：<br /><b
                  >mongodb+srv://user:password@(host:port)/db_name</b
                ><br /><br />
                postgresql 的 dsn 支持指定 schema，在 dsn 后添加参数示例：<br />
                <b>?search_path=schema1&search_path=schema2</b><br /><br />
                如果数据库是<b>sqlite</b>，填写 sqlite db 文件本地绝对路径，<br />
                示例：<b>/tmp/test.db</b> 或 <b>C:\Users\Desktop\test.db</b>
              </div>
            </el-tooltip>
            <el-link
              style="margin-left: 10px; font-size: 16px;"
              type="primary"
              @click="listTables(true)"
              >获取表名</el-link
            >
          </el-form-item>
          <el-form-item label="表名" prop="tableName">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.tableName"
              placeholder="请选择表名，支持多选"
              clearable
              filterable
              multiple
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-checkbox
                v-model="isSelectedAllTable"
                @change="selectAllTables"
                style="padding: 10px 0px 15px 20px; font-weight: bold; color: #409EFF;"
                >全选</el-checkbox
              >
              <el-badge
                v-model="ruleForm.tableName.length"
                type="primary"
                style="padding: 10px 18px 12px 0px; position: absolute; right: 0;"
              ></el-badge>
              <el-option
                v-for="(item, index) in tableNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                表名需采用单数形式，例如应使用"role"，而非"roles"。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="嵌入 Model" v-if="isShowEmbedModel">
            <el-switch v-model="ruleForm.embed"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                gorm.Model 结构体字段对应表的 id、created_at、<br />
                updated_at、deleted_at 这 4 个列名，支持软删除。<br />
                如果表包含这些列名，请开启嵌入 Model，<br />
                如果表不包含这些列名，请关闭嵌入 Model。
              </div>
            </el-tooltip>
          </el-form-item>
          <!-- <el-form-item label="初始化 db">
            <el-switch v-model="ruleForm.includeInitDB"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                选中这个选项生成的 dao 代码包括了 mysql 和 redis 初始化代码，<br />
                通常用在基于 protobuf 创建的 web 或 gRPC 的项目中，如果是<br />
                基于表 sql 生成的 web 或 gRPC 项目则不需要选中。
              </div>
            </el-tooltip>
          </el-form-item> -->
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isDownloading"
            :loading="isDownloading"
            >下载代码</el-button
          >
          <DownloadCode
            ref="downloadCode"
            @update-is-downloading="updateIsDownloading"
          ></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "WebModel",
  data() {
    return {
      title: "生成 model 代码",
      path: "web-model",
      helpInfo: `
      <p>基于表 sql 生成对应 Go 的结构体。</p>
      <br />
      <p>解压文件，将文件夹<b>internal</b>移动到项目代码文件夹下。</p>
      `,
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isShowEmbedModel: true,
      isDownloading: false,

      ruleForm: {
        dsnAddr: "",
        tableName: [],
        dbDriver: "",
        embed: false,
      },
      rules: {
        dsnAddr: [
          { required: true, message: "请输入数据源 dsn", trigger: "blur" },
        ],
        tableName: [
          { required: true, message: "请选择表名", trigger: "change" },
        ],
        dbDriver: [
          { required: true, message: "请选择数据库类型", trigger: "blur" },
        ],
      },

      tableNames: [],
      isSelectedAllTable: false,
      dbDriverNames: [
        { label: "mysql", value: "mysql" },
        { label: "mongodb", value: "mongodb" },
        { label: "postgresql", value: "postgresql" },
        { label: "sqlite", value: "sqlite" },
      ],
    };
  },
  created: function() {
    this.adaptiveWindows();
    this.listDbDrivers();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;

        if (data.code == 0) {
          this.ruleForm.dsnAddr = data.data.dsn;
          this.ruleForm.dbDriver = data.data.dbDriver;
          // this.ruleForm.embed = data.data.embed;

          this.checkDBDriver(this.ruleForm.dbDriver);
          this.listTables(false);
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          var selectTableNames = this.ruleForm.tableName.join(",");
          const arg = `web model --db-driver=${this.ruleForm.dbDriver} --db-dsn=${this.ruleForm.dsnAddr} --db-table=${selectTableNames} --embed=${this.ruleForm.embed}`;
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    clearFormData() {
      this.tableNames = [];
      this.ruleForm = {
        tableName: [],
        embed: false,
      };
    },
    listTables(isTip) {
      if (this.ruleForm.dsnAddr == "") {
        return;
      }
      const apiUrl = appConfig.spongeServiceAddr + "/listTables";
      const formdata = {
        dsn: this.ruleForm.dsnAddr,
        dbDriver: this.ruleForm.dbDriver,
      };
      axios
        .post(apiUrl, formdata, { timeout: 3000 })
        .then(res => {
          const { data } = res;
          if (isTip) {
            data.code == 0
              ? this.$message.success("获取表名成功")
              : this.$message.error("获取表名失败，" + data.msg);
          }

          this.tableNames = data.data;
        })
        .catch(err => {
          if (isTip) {
            this.$message.error(
              "获取表名失败，请检查" +
                this.ruleForm.dbDriver +
                " dsn 地址是否正确、或者" +
                this.ruleForm.dbDriver +
                "服务是否运行。",
            );
          }
          // console.log(err);
        });
    },
    listDbDrivers() {
      const apiUrl = appConfig.spongeServiceAddr + "/listDrivers";
      axios
        .get(apiUrl)
        .then(res => {
          const { data } = res;
          if (data.code == 0) {
            this.dbDriverNames = data.data;
          }
        })
        .catch(err => {
          // console.log(err);
        });
    },
    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(其他)";
        this.$forceUpdate(); // 强制更新
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      if (val.length === this.tableNames.length) {
        this.isSelectedAllTable = true;
      } else {
        this.isSelectedAllTable = false;
      }
      this.value = val;
      this.$forceUpdate();
    },
    selectAllTables(val) {
      this.ruleForm.tableName = [];
      if (val) {
        this.tableNames.forEach(item => {
          this.ruleForm.tableName.push(item.value);
        });
      }
    },

    selectChangeDB(val) {
      this.value = val;
      this.$forceUpdate();
      this.checkDBDriver(val);
    },

    checkDBDriver(val) {
      if (val == "mongodb") {
        this.isShowEmbedModel = false;
      } else {
        this.isShowEmbedModel = true;
      }
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}
</style>



