<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <a
            href="https://go-sponge.com/zh/guide/customize/template-protobuf.html"
            target="_blank"
            style="color: #32CD32"
            >基于 protobuf 和自定义模板生成代码</a
          >
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item
            label="自定义模板目录"
            prop="templateDir"
            style="margin-top: 10px;"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.templateDir"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                自定义模板代码所在文件夹的绝对路径。<br />
                windows 环境示例：D:\tmp\template-dir<br />
                linux 环境示例：/tmp/template-dir<br /><br />
                注意：如果选择了多个 proto 文件，目录下的所有<br />
                模板文件名必须使用 proto 文件名。<br />
                示例：<b>{{ tplFileNameExample }}</b>
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="proto 文件" prop="protobufFile">
            <el-upload
              class="upload-demo"
              drag
              multiple
              accept=".proto"
              action="#"
              :auto-upload="false"
              :on-change="handleProtoOnchange"
              :on-remove="handleProtoOnRemove"
              :file-list="protoFileList"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                <em>选择 proto 文件</em>，支持多文件。
              </div>
            </el-upload>
          </el-form-item>

          <el-form-item
            label="依赖 proto 文件目录"
            prop="depProtoDir"
            style="margin-top: 50px;"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.depProtoDir"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                依赖的 proto 文件所在文件夹的绝对路径，例如 a.proto<br />
                依赖 b.proto，则把 b.proto 文件放到该文件夹下。<br />
                windows 环境示例：D:\tmp\dep-proto-dir<br />
                linux 环境示例：/tmp/dep-proto-dir
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="自定义字段" prop="jsonFile">
            <el-upload
              class="upload-demo"
              ref="upload"
              drag
              accept=".json"
              action="#"
              :auto-upload="false"
              :on-change="handleOnchange"
              :on-remove="handleOnRemove"
              :file-list="fileList"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                <em>选择 json 文件</em>，仅限一个文件。
              </div>
            </el-upload>
          </el-form-item>
        </el-form>
        <div style="margin-bottom: 50px;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            size="default"
            @click="showTemplateInfo('ruleForm')"
            :disabled="isShowingInfo"
            :loading="isShowingInfo"
            >查看模板信息</el-button
          >
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isDownloading"
            :loading="isDownloading"
            >下载代码</el-button
          >
          <DownloadCode
            ref="downloadCode"
            @update-is-downloading="updateIsDownloading"
          ></DownloadCode>
          <ShowRepoDir ref="showRepoDir"></ShowRepoDir>
        </div>
      </div>

      <el-dialog
        top="5vh"
        title="模板信息"
        :visible.sync="showTplInfoDialogVisible"
        width="66%"
        center
      >
        <el-input
          type="textarea"
          :rows="20"
          autosize
          readonly
          v-model="tplContent"
        >
        </el-input>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
import ShowRepoDir from "@/components/ShowRepoDir";
export default {
  name: "CustomTplProtobuf",
  data() {
    return {
      title: "",
      path: "custom-tpl-protobuf",
      helpInfo: `
      <p>根据 protobuf、json、自定义模板生成代码，更多开发细节，请参考 <a href="https://go-sponge.com/zh/guide/customize/template-protobuf.html" target="_blank">开发指南</a>。</p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isDownloading: false,
      isShowingInfo: false,

      protoFileList: [],
      fileList: [],
      ruleForm: {
        protobufFile: "",
        depProtoDir: "",
        templateDir: "",
        jsonFile: "",
      },
      rules: {
        protobufFile: [
          { required: true, message: "请选择 proto 文件", trigger: "change" },
        ],
        templateDir: [
          {
            required: true,
            message: "请输入模板代码所在的目录绝对路径",
            trigger: "blur",
          },
        ],
      },

      showTplInfoDialogVisible: false,
      tplContent: "",
      tplFileNameExample: "{{.Proto.FileNamePrefix}}.go.tmpl",
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.protobufFile = "";
          this.ruleForm.depProtoDir = data.data.depProtoDir;
          this.ruleForm.templateDir = data.data.templateDir;
          if (data.data.fields !== "") {
            this.ruleForm.jsonFile = data.data.fields;
            this.fileList.push({
              name: data.data.fields.split("/").pop(),
              raw: data.data.fields,
              status: "fromRecord",
            });
          }
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode, ShowRepoDir },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // proto 文件处理
          let fd = new FormData();
          this.protoFileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = appConfig.spongeServiceAddr + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                const protoFile = res.data.data;
                // 自定义文件处理
                if (this.fileList.length === 1) {
                  if (this.fileList[0].status === "fromRecord") {
                    this.downloadZipCode(protoFile, this.fileList[0].raw);
                  } else {
                    // 上传自定义字段文件
                    let fd = new FormData();
                    this.fileList.forEach(item => {
                      fd.append("files", item.raw);
                    });
                    const uploadURL =
                      appConfig.spongeServiceAddr + "/uploadFiles";
                    axios
                      .post(uploadURL, fd)
                      .then(res => {
                        if (res.data.code == 0) {
                          this.downloadZipCode(protoFile, res.data.data);
                        } else {
                          this.$message.error(res.data.msg);
                        }
                      })
                      .catch(err => {
                        this.$message.error("下载代码失败");
                      });
                  }
                } else {
                  this.downloadZipCode(protoFile, "");
                }
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("下载代码失败");
            });
        } else {
          return false;
        }
      });
    },

    showTemplateInfo(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // proto 文件处理
          let fd = new FormData();
          this.protoFileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = appConfig.spongeServiceAddr + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                const protoFile = res.data.data;
                // 自定义文件处理
                if (this.fileList.length === 1) {
                  if (this.fileList[0].status === "fromRecord") {
                    this.getTemplateInfo(protoFile, this.fileList[0].raw);
                  } else {
                    // 上传自定义字段文件
                    let fd = new FormData();
                    this.fileList.forEach(item => {
                      fd.append("files", item.raw);
                    });
                    const uploadURL =
                      appConfig.spongeServiceAddr + "/uploadFiles";
                    axios
                      .post(uploadURL, fd)
                      .then(res => {
                        if (res.data.code == 0) {
                          this.getTemplateInfo(protoFile, res.data.data);
                        } else {
                          this.$message.error(res.data.msg);
                        }
                      })
                      .catch(err => {
                        this.$message.error("下载代码失败");
                      });
                  }
                } else {
                  this.getTemplateInfo(protoFile, "");
                }
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("下载代码失败");
            });
        } else {
          return false;
        }
      });
    },

    downloadZipCode(protoFile, jsonFile) {
      // 下载代码
      const apiUrl = appConfig.spongeServiceAddr + "/generate";
      const arg = `template protobuf --protobuf-file=${protoFile} --dep-proto-dir=${this.ruleForm.depProtoDir} --tpl-dir=${this.ruleForm.templateDir} --fields=${jsonFile}`;
      this.$refs.downloadCode.execution(apiUrl, this.path, arg, this.helpInfo);
    },

    getTemplateInfo(protoFile, jsonFile) {
      this.updateIsShowingInfo(true);
      // 查看模板信息
      const apiUrl = appConfig.spongeServiceAddr + "/getTemplateInfo";
      const arg = `template protobuf --protobuf-file=${protoFile} --dep-proto-dir=${this.ruleForm.depProtoDir} --tpl-dir=${this.ruleForm.templateDir} --fields=${jsonFile} --only-print=true`;
      const bodyData = {
        path: this.path,
        arg: arg,
      };
      axios
        .post(apiUrl, bodyData)
        .then(res => {
          this.updateIsShowingInfo(false);
          if (res.data.code == 0) {
            this.tplContent = res.data.data;
            this.showTplInfoDialogVisible = true;
          } else {
            this.$message.error(res.data.msg);
          }
        })
        .catch(err => {
          this.updateIsShowingInfo(false);
          this.$message.error(err.response.data.msg);
        });
    },

    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    updateIsShowingInfo(value) {
      this.isShowingInfo = value;
    },
    clearFormData() {
      this.protoFileList = [];
      this.fileList = [];
      this.ruleForm = {};
    },

    handleProtoOnchange(file, fileList) {
      this.ruleForm.protobufFile = file.name;
      this.protoFileList = fileList;

      if (this.protoFileList.length !== 0) {
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },
    handleProtoOnRemove(file, fileList) {
      this.protoFileList = fileList;
      if (this.protoFileList.length === 0) {
        this.ruleForm.protobufFile = "";
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },
    handleOnchange(file, fileList) {
      this.ruleForm.jsonFile = file.name;
      this.fileList = fileList;

      if (fileList.length > 1) {
        this.fileList = fileList.slice(-1);
      }
    },
    handleOnRemove(file, fileList) {
      this.fileList = fileList;
      if (this.fileList.length === 0) {
        this.ruleForm.jsonFile = "";
      }
    },

    showMutiRepoDir() {
      this.$refs.showRepoDir.isShowMutiDialog = true;
    },
    showMonoRepoDir() {
      this.$refs.showRepoDir.isShowMonoDialog = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.title a {
  text-decoration: none;
}
.title a:hover {
  text-decoration: underline;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.upload-demo /deep/ .el-upload-dragger {
  height: 120px;
  width: 300px;
  margin-bottom: -10px;
  margin-top: 3px;
}
.el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
.el-upload__text {
  line-height: 1.5;
  margin-bottom: 20px;
}

.showProtoDemoDiv {
  width: 85%;
  margin: auto;
  font-size: 16px;
  color: black;
  background: rgba(255, 255, 255, 0.5);
  line-height: 20px;
  border: 1px solid #ccc;
  padding: 5px 20px;
  margin-bottom: 20px;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.tooltip-content b {
  color: #bb6f00;
}

.el-dialog__wrapper /deep/ .el-dialog .el-dialog__body {
  padding: 10px 50px 30px 50px;
}

.el-textarea /deep/ .el-textarea__inner {
  background: rgba(220, 220, 200, 0.3);
  border: 1px solid #ddd;
}
</style>



