<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <a
            href="https://go-sponge.com/zh/guide/grpc/based-on-protobuf.html"
            target="_blank"
            style="color: #2ba207"
            >基于 protobuf 创建 gRPC 服务</a
          >
          <span style="color: #666; font-weight:normal;">
            —— 生成通用的微服务代码</span
          >
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="proto 文件" prop="protobufFile">
            <el-link
              style="margin-left: 10%;padding-bottom: 5px; font-size: 16px;"
              type="primary"
              @click="showProtoFileDemo"
              >查看 protobuf 文件示例</el-link
            >
            <el-upload
              class="upload-demo"
              drag
              multiple
              accept=".proto"
              action="#"
              :auto-upload="false"
              :on-change="handleOnchange"
              :on-remove="handleOnRemove"
              :file-list="fileList"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                <em>选择 proto 文件</em>，支持多文件。
              </div>
              <!-- <div class="el-upload__tip" slot="tip">
                只能上传 proto 文件
              </div> -->
            </el-upload>
          </el-form-item>

          <el-form-item
            label="服务名称"
            prop="serverName"
            style="margin-top: 50px;"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.serverName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                如果名称包括中划线"-"，会转换为下划线"_"，<br />
                示例：your-server 🡆 your_server
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="module 名称" prop="moduleName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                go.mod 文件的 module 名称。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="项目名称" prop="projectName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.projectName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                如果名称包括大写字母，会转换为小写，并用中划线分隔，<br />
                示例：yourProject 🡆 your-project
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="docker 镜像仓库">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.repoAddr"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                docker 镜像仓库地址，默认值是<b>image-repo-host</b>，这个参数<br />
                用在构建 docker 镜像、k8s 的部署脚本上，如果使用 docker 官方<br />
                镜像仓库，只需填写注册 docker 仓库的用户名。如果使用私有仓<br />
                库地址，需要填写完整仓库地址。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="大仓库类型">
            <el-switch v-model="ruleForm.suitedMonoRepo"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                默认关闭，表示生成适合<b>单体应用单仓库(monolith)</b>或<b
                  >微服务多<br />
                  仓库(multi-repo)</b
                >类型的服务代码，即使把多个服务代码放在一个<br />
                git 仓库下，不同服务代码之间不可复用。<br />点击查看<el-button
                  type="text"
                  @click="showMutiRepoDir"
                  >目录结构示例</el-button
                >。<br /><br />
                如果开启，表示生成适合<b>微服务单仓库(mono-repo)</b>类型的服务<br />
                代码，多个服务代码放在一个 git 仓库下，不同服务之间的代码可以<br />
                复用。如果后面在页面生成的模块代码移动到此服务，也需要开启<br />这个选项。<br />
                点击查看<el-button type="text" @click="showMonoRepoDir"
                  >目录结构示例</el-button
                >。
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isDownloading"
            :loading="isDownloading"
            >下载代码</el-button
          >
          <DownloadCode
            ref="downloadCode"
            @update-is-downloading="updateIsDownloading"
          ></DownloadCode>
          <ShowRepoDir ref="showRepoDir"></ShowRepoDir>
        </div>
      </div>

      <el-dialog
        top="5vh"
        title="user.proto"
        :visible.sync="showProtoDemoDialogVisible"
        width="66%"
        center
      >
        <el-input
          type="textarea"
          :rows="20"
          autosize
          readonly
          v-model="protoDemoContent"
        >
        </el-input>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
import ShowRepoDir from "@/components/ShowRepoDir";
export default {
  name: "MicroRPCPb",
  data() {
    return {
      title: "",
      path: "micro-rpc-pb",
      helpInfo: `
      <p><b>基于 protobuf 创建 gRPC 服务</b> 生成了通用的 gRPC 服务代码，更多开发细节，请参考 <a href="https://go-sponge.com/zh/guide/grpc/based-on-protobuf.html" target="_blank">开发指南</a>。</p>
      <br />
      <p>1. 解压文件。</p>
      <p>2. 打开终端，执行命令生成代码:  <b style="color: green">make proto</b></p>
      <p>3. 打开文件 <span style="color: chocolate">internal/service/xxx.go</span>, 根据模板代码示例填充代码替换 <span style="color: chocolate">panic("implement me")</span></p>
      <p>4. 编译和运行服务:  <b style="color: green">make run</b></p>
      <p>5. 使用 <b>Goland</b> 或 <b>VSCode</b> 打开文件 <span style="color: chocolate">internal/service/xxx_client_test.go</span>，测试 api。</p>
      <br />
      <p>技术栈：<b>Go、gRPC、Protobuf、Zap、Prometheus、OpenTracing</b> 等。</p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isDownloading: false,

      fileList: [],
      ruleForm: {
        moduleName: "",
        serverName: "",
        projectName: "",
        repoAddr: "",
        protobufFile: "",
        suitedMonoRepo: false,
      },
      rules: {
        moduleName: [
          { required: true, message: "请输入 go module 名称", trigger: "blur" },
        ],
        serverName: [
          { required: true, message: "请输入服务名称", trigger: "blur" },
        ],
        projectName: [
          { required: true, message: "请输入项目名称", trigger: "blur" },
        ],
        protobufFile: [
          { required: true, message: "请选择 proto 文件", trigger: "change" },
        ],
      },

      showProtoDemoDialogVisible: false,
      protoDemoContent: `syntax = "proto3";

package api.user.v1;

// 第一个 user 是 go module 名称，第二个 user 是服务名称，v1是版本号
option go_package = "user/api/user/v1;v1";

service user {
  // Login 登录注释
  rpc Login(LoginRequest) returns (LoginReply) {}

  rpc ClientSteam(stream HelloRequest) returns (HelloReply) {}
  rpc ServerSteam(HelloRequest) returns (stream HelloReply) {}
  rpc BidiSteam(stream HelloRequest) returns (stream HelloReply) {}
}

message LoginRequest {
  string email = 1;
  string password = 2;
}

message LoginReply {
  string token = 1;
}

message HelloRequest {
  string name = 1;
}

message HelloReply {
  string msg = 1;
}
`,
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          this.ruleForm.projectName = data.data.projectName;
          this.ruleForm.repoAddr = data.data.repoAddr;
          this.ruleForm.protobufFile = "";
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode, ShowRepoDir },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 上传文件
          let fd = new FormData();
          // fd.append("param", "param value");
          this.fileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = appConfig.spongeServiceAddr + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                // 下载代码
                const apiUrl = appConfig.spongeServiceAddr + "/generate";
                const arg = `micro rpc-pb --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --protobuf-file=${res.data.data} --suited-mono-repo=${this.ruleForm.suitedMonoRepo}`;
                this.$refs.downloadCode.execution(
                  apiUrl,
                  this.path,
                  arg,
                  this.helpInfo,
                );
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("下载代码失败");
            });
        } else {
          return false;
        }
      });
    },
    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    clearFormData() {
      this.fileList = [];
      this.ruleForm = {};
    },
    handleOnchange(file, fileList) {
      this.ruleForm.protobufFile = file.name;
      this.fileList = fileList;

      if (fileList.length !== 0) {
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },
    handleOnRemove(file, fileList) {
      this.fileList = fileList;
      if (fileList.length === 0) {
        this.ruleForm.protobufFile = "";
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },

    showProtoFileDemo() {
      this.showProtoDemoDialogVisible = true;
    },

    showMutiRepoDir() {
      this.$refs.showRepoDir.isShowMutiDialog = true;
    },
    showMonoRepoDir() {
      this.$refs.showRepoDir.isShowMonoDialog = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.upload-demo /deep/ .el-upload-dragger {
  height: 60%;
  width: 150%;
}

.el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
.el-upload__text {
  line-height: 1.5;
  margin-bottom: 20px;
}

.showProtoDemoDiv {
  width: 85%;
  margin: auto;
  font-size: 16px;
  color: black;
  background: rgba(255, 255, 255, 0.5);
  line-height: 20px;
  border: 1px solid #ccc;
  padding: 5px 20px;
  margin-bottom: 20px;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.tooltip-content b {
  color: #bb6f00;
}

.title a {
  text-decoration: none;
}
.title a:hover {
  text-decoration: underline;
}

.el-dialog__wrapper /deep/ .el-dialog .el-dialog__body {
  padding: 10px 50px 30px 50px;
}

.el-textarea /deep/ .el-textarea__inner {
  background: rgba(220, 220, 200, 0.3);
  border: 1px solid #ddd;
}
</style>



