<template>
  <div>
    <el-dialog
      title="单体应用单仓库(monolith)或微服务多仓库(multi-repo)目录结构"
      top="7vh"
      width="60%"
      :visible.sync="isShowMutiDialog"
    >
      <pre>{{ mutiRepoText }}</pre>
    </el-dialog>

    <el-dialog
      title="微服务单仓库(mono-repo)目录结构(也就是大仓库目录结构)"
      width="66%"
      :visible.sync="isShowMonoDialog"
    >
      <pre>{{ monoRepoText }}</pre>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ShowRepoDir",
  computed: {
    mutiRepoFormattedText() {
      return this.mutiRepoText.replace(/\n/g, "<br>");
    },
  },
  data() {
    return {
      isShowMutiDialog: false,
      isShowMonoDialog: false,
      mutiRepoText: ` .
├── api            # protobuf 文件和生成的*pb.go 目录
├── assets         # 其他与资源库一起使用的资产(图片、logo 等)目录
├── cmd            # 程序入口目录
├── configs        # 配置文件的目录
├── deployments    # 裸机、docker、k8s 部署脚本目录
├── docs           # 设计文档和界面文档目录
├── internal       # 项目内部代码目录
│    ├── cache        # 基于业务包装的缓存目录
│    ├── config       # Go 结构的配置文件目录
│    ├── dao          # 数据访问目录
│    ├── ecode        # 自定义业务错误代码目录
│    ├── handler      # http 的业务功能实现目录
│    ├── model        # 数据库模型目录
│    ├── routers      # http 路由目录
│    ├── rpcclient    # 连接 gRP 服务的客户端目录
│    ├── server       # 服务入口，包括 http、gRPC 等
│    ├── service      # gRPC 的业务功能实现目录
│    └── types        # http 的请求和响应类型目录
├── pkg            # 外部应用程序可以使用的库目录
├── scripts        # 执行脚本目录
├── test           # 额外的外部测试程序和测试数据
├── third_party    # 依赖第三方 protobuf 文件或其他工具的目录
├── Makefile       # 开发、测试、部署相关的命令集合
├── go.mod         # go 模块依赖关系和版本控制文件
└── go.sum         # go 模块依赖项的密钥和校验文件
      `,
      monoRepoText: ` .
├── api
│    ├── server1       # 服务1的 protobuf 文件和生成的*pb.go 目录
│    ├── server2       # 服务2的 protobuf 文件和生成的*pb.go 目录
│    ├── server3       # 服务3的 protobuf 文件和生成的*pb.go 目录
│    └── ...
├── server1        # 服务1的代码目录，与微服务多仓库(multi-repo)目录结构基本一样
├── server2        # 服务2的代码目录，与微服务多仓库(multi-repo)目录结构基本一样
├── server3        # 服务3的代码目录，与微服务多仓库(multi-repo)目录结构基本一样
├── ...
├── third_party    # 依赖的第三方 protobuf 文件
├── go.mod         # go 模块依赖关系和版本控制文件
└── go.sum         # go 模块依赖项的密钥和校验和文件

`,
    };
  },
};
</script>

<style scoped>
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
  text-align: left;
  margin-left: 8%;
  margin-right: 8%;
  padding-left: 30px;
  border: 1px solid #ccc;
}
</style>



