<template>
  <div>
    <el-dialog
      class="helper-dialog"
      title="Downloaded Successfully"
      :visible.sync="downloadStatusDialogVisible"
      width="60%"
      center
    >
      <h3 style="margin-left:30px">Help for use:</h3>
      <div
        style="padding:10px 30px; font-size: 16px;"
        v-html="dialogContent"
      ></div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "DownloadCode",
  data() {
    return {
      downloadStatusDialogVisible: false,
      dialogContent: "",
    };
  },
  methods: {
    execution(url, path, arg, helpInfo) {
      const formdata = {
        path: path,
        arg: arg,
      };
      this.$emit("update-is-downloading", true);

      axios
        .post(url, formdata, { responseType: "blob" })
        .then(res => {
          const { data, headers } = res;
          const fileName = headers["content-disposition"];
          const blob = new Blob([data], { type: headers["content-type"] });
          let dom = document.createElement("a");
          let url = window.URL.createObjectURL(blob);
          dom.href = url;
          dom.download = decodeURI(fileName);
          dom.style.display = "none";
          document.body.appendChild(dom);
          dom.click();
          dom.parentNode.removeChild(dom);
          window.URL.revokeObjectURL(url);

          this.dialogContent = helpInfo;
          this.downloadStatusDialogVisible = true;
          this.$emit("update-is-downloading", false);
        })
        .catch(err => {
          console.log(err);
          const { headers } = err.response;
          if (headers["err-msg"] != "") {
            this.$message.error(headers["err-msg"]);
          } else {
            this.$message.error(err.message);
          }
          this.$emit("update-is-downloading", false);
        });
    },
  },
};
</script>

<style scoped>
.helper-dialog /deep/ .el-dialog__body {
  background: url("../../assets/body-background.png") repeat;
  padding: 10px 50px 30px 50px;
  color: #111;
}

.helper-dialog /deep/ .el-dialog__header {
  padding: 14px 14px;
}
</style>



