<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <a
            href="https://go-sponge.com/guide/grpc/based-on-sql.html"
            target="_blank"
            style="color: #99c400"
            >Create gRPC server based on sql</a
          >
          <span style="color: #666; font-weight:normal;">
            — generate complete microservice code</span
          >
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="database type" prop="dbDriver">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.dbDriver"
              placeholder="please select database type"
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChangeDB"
            >
              <el-option
                v-for="(item, index) in dbDriverNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                After selecting the database type, the generated code<br />
                will automatically match that database type.
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="database dsn" prop="dsnAddr">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.dsnAddr"
              placeholder="user:password@(127.0.0.1:3306)/db_name"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If the database is <b>mysql</b>, <b>mongodb</b>,
                <b>postgresql</b> fill in the dsn address.<br />
                dsn format: <b>user:password@(host:port)/db_name</b><br />
                Note: user and password cannot contain special characters
                <b>@</b> <b>:</b> <b>(</b> <b>)</b> <br /><br />
                The dsn of mongodb supports SRV records, with the format:<br />
                <b>mongodb+srv://user:password@(host:port)/db_name</b
                ><br /><br />
                The dsn of postgresql supports the specified schema, example of
                adding<br />
                parameters after dsn
                <b>?search_path=schema1&search_path=schema2</b><br /><br />
                If the database is <b>sqlite</b>, fill in the local absolute
                path of the sqlite db file.<br />
                Example: <b>/tmp/test.db</b> or
                <b>C:\Users\Desktop\test.db</b>
              </div>
            </el-tooltip>
            <el-link
              style="margin-left: 10px; font-size: 16px;"
              type="primary"
              @click="listTables(true)"
              >get tables</el-link
            >
          </el-form-item>
          <el-form-item label="table name" prop="tableName">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.tableName"
              placeholder="supports multiple selection"
              clearable
              filterable
              multiple
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-checkbox
                v-model="isSelectedAllTable"
                @change="selectAllTables"
                style="padding: 10px 0px 15px 20px; font-weight: bold; color: #409EFF;"
                >select all</el-checkbox
              >
              <el-badge
                v-model="ruleForm.tableName.length"
                type="primary"
                style="padding: 10px 18px 12px 0px; position: absolute; right: 0;"
              ></el-badge>
              <el-option
                v-for="(item, index) in tableNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Table names must be in singular form. For example,<br />
                use "role" instead of "roles".<br />
                If you need to add a new data table, please go to<br />
                <b>[Public] → [Generate Service CRUD Code]</b><br />
                in the menu to generate the code, and then move it<br />
                to the appropriate location in the project.
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="server name" prop="serverName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.serverName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If the name contains hyphens, they will be converted to
                underscores.<br />
                example: your-server 🡆 your_server
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="module name" prop="moduleName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                module name for go.mod file.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="project name" prop="projectName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.projectName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                If the name contains uppercase letters, they will be<br />
                converted to lowercase and separated by underscores.<br />
                example: yourProject 🡆 your-project
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="docker repository">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.repoAddr"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Docker image repository address, the default value is
                <b>image-repo-host</b>,<br />
                this parameter is used to build docker images, k8s deployment
                scripts, <br />
                if you use the official docker image repository, just fill in
                the user name<br />
                of the registered docker repository, if you use the private
                repository<br />
                address, you need to fill in the full repository address.<br />
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="embed model" v-if="isShowEmbedModel">
            <el-switch v-model="ruleForm.embed"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                The gorm.Model struct field corresponds to the four column names
                <br />
                id, created_at, updated_at, and deleted_at in the table,
                supports soft deletion.
                <br />
                If the table contains these column names, enable it.<br />
                If the table does not contain these column names, disable it.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="CRUD API extend">
            <el-switch v-model="ruleForm.isExtendedApi"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Default off, generating 5 basic CRUD API codes.<br />
                If enabled, in addition to the 5 basic CRUD APIs,<br />
                an additional 4 APIs have been added, namely<br />
                DeleteByIDs, GetByCondition, ListByIDs, ListByLastID.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="large repository type">
            <el-switch v-model="ruleForm.suitedMonoRepo"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Disabled by default, it indicates that the generation of
                server<br />
                code suitable for a
                <b>monolithic application repository (monolith)</b><br />
                or <b>microservice multiple repository (multi-repo)</b>, where
                even if<br />
                multiple server codes are placed in one git repository, the
                codes<br />
                between different servers are not reusable.<br />
                Click to view
                <el-button type="text" @click="showMutiRepoDir"
                  >directory structure example</el-button
                >. <br /><br />
                If enabled, it indicates that the generation of server code
                suitable<br />
                for a <b>microservice monolithic repository (mono-repo)</b>,
                where<br />
                multiple server codes are placed in one git repository, and
                the<br />
                codes between different servers can be reused. If modules
                code<br />
                generated later on the page are moved to this server, this
                option<br />
                also needs to be enabled.<br />
                Click to view
                <el-button type="text" @click="showMonoRepoDir"
                  >directory structure example</el-button
                >.
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">Reset</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isDownloading"
            :loading="isDownloading"
            >Download code</el-button
          >
          <DownloadCode
            ref="downloadCode"
            @update-is-downloading="updateIsDownloading"
          ></DownloadCode>
          <ShowRepoDir ref="showRepoDir"></ShowRepoDir>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/en/DownloadCode";
import ShowRepoDir from "@/components/en/ShowRepoDir";

export default {
  name: "MicroRPC",
  data() {
    return {
      title: "",
      path: "micro-rpc",
      helpInfo: `
      <p><b>Create gRPC server based on sql</b> generates a complete microservice code including CRUD API, for more development details, please refer to the <a href="https://go-sponge.com/guide/grpc/based-on-sql.html" target="_blank">Development Guide</a>.</p>
      <br />
      <p>1. Unzip the code file.</p>
      <p>2. Open a terminal and execute the command to generate code:  <b style="color: green">make proto</b></p>
      <p>3. Compile and run server:   <b style="color: green">make run</b></p>
      <p>4. Open the file using <b>Goland</b> or <b>VSCode</b> <span style="color: chocolate">internal/service/xxx_client_test.go</span>, test or performance test the api.</p>
      <br />
      <p>Technology stack: <b>Go, gRPC, {{.DBDriver}}, go-redis, Protobuf, Zap, Prometheus, OpenTracing</b>, etc.</p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isShowEmbedModel: true,
      isDownloading: false,

      ruleForm: {
        dsnAddr: "",
        tableName: [],
        dbDriver: "",
        moduleName: "",
        serverName: "",
        projectName: "",
        repoAddr: "",
        embed: false,
        isExtendedApi: false,
        suitedMonoRepo: false,
      },
      rules: {
        dsnAddr: [
          {
            required: true,
            message: "please type the database dsn address",
            trigger: "blur",
          },
        ],
        tableName: [
          {
            required: true,
            message: "please select a table name",
            trigger: "change",
          },
        ],
        dbDriver: [
          {
            required: true,
            message: "please select a database type",
            trigger: "change",
          },
        ],
        moduleName: [
          {
            required: true,
            message: "please type the name of the go module",
            trigger: "blur",
          },
        ],
        serverName: [
          {
            required: true,
            message: "please type the name of the server",
            trigger: "blur",
          },
        ],
        projectName: [
          {
            required: true,
            message: "please type the project name",
            trigger: "blur",
          },
        ],
      },

      tableNames: [],
      isSelectedAllTable: false,
      dbDriverNames: [
        { label: "mysql", value: "mysql" },
        { label: "mongodb", value: "mongodb" },
        { label: "postgresql", value: "postgresql" },
        { label: "sqlite", value: "sqlite" },
      ],
    };
  },
  created: function() {
    this.adaptiveWindows();
    this.listDbDrivers();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.dsnAddr = data.data.dsn;
          this.ruleForm.dbDriver = data.data.dbDriver;
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          this.ruleForm.projectName = data.data.projectName;
          this.ruleForm.repoAddr = data.data.repoAddr;
          // this.ruleForm.embed = data.data.embed;

          this.checkDBDriver(this.ruleForm.dbDriver);
          this.listTables(false);
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode, ShowRepoDir },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          var selectTableNames = this.ruleForm.tableName.join(",");
          const arg = `micro rpc --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --db-driver=${this.ruleForm.dbDriver} --db-dsn=${this.ruleForm.dsnAddr} --db-table=${selectTableNames} --embed=${this.ruleForm.embed} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --db-driver=${this.ruleForm.dbDriver} --db-dsn=${this.ruleForm.dsnAddr} --db-table=${selectTableNames} --embed=${this.ruleForm.embed} --suited-mono-repo=${this.ruleForm.suitedMonoRepo} --extended-api=${this.ruleForm.isExtendedApi}`;
          var helpInfo = this.helpInfo;
          if (this.ruleForm.dbDriver === "mongodb") {
            helpInfo = helpInfo.replace("{{.DBDriver}}", "Mongo");
          } else {
            helpInfo = helpInfo.replace("{{.DBDriver}}", "Gorm");
          }
          this.$refs.downloadCode.execution(apiUrl, this.path, arg, helpInfo);
        } else {
          return false;
        }
      });
    },
    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    clearFormData() {
      this.tableNames = [];
      this.ruleForm = {
        tableName: [],
        embed: false,
        suitedMonoRepo: false,
      };
    },
    listTables(isTip) {
      if (this.ruleForm.dsnAddr == "") {
        return;
      }
      const apiUrl = appConfig.spongeServiceAddr + "/listTables";
      const formdata = {
        dsn: this.ruleForm.dsnAddr,
        dbDriver: this.ruleForm.dbDriver,
      };
      axios
        .post(apiUrl, formdata, { timeout: 3000 })
        .then(res => {
          const { data } = res;
          if (isTip) {
            data.code == 0
              ? this.$message.success("Get table name success")
              : this.$message.error("Failed to get table name");
          }

          this.tableNames = data.data;
        })
        .catch(err => {
          if (isTip) {
            this.$message.error(
              "Failed to get table name, please check if " +
                this.ruleForm.dbDriver +
                "  dsn address is correct or " +
                this.ruleForm.dbDriver +
                " service is running.",
            );
          }
          // console.log(err);
        });
    },
    listDbDrivers() {
      const apiUrl = appConfig.spongeServiceAddr + "/listDrivers";
      axios
        .get(apiUrl)
        .then(res => {
          const { data } = res;
          if (data.code == 0) {
            this.dbDriverNames = data.data;
          }
        })
        .catch(err => {
          // console.log(err);
        });
    },
    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(other)";
        this.$forceUpdate();
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      if (val.length === this.tableNames.length) {
        this.isSelectedAllTable = true;
      } else {
        this.isSelectedAllTable = false;
      }
      this.value = val;
      this.$forceUpdate();
    },
    selectAllTables(val) {
      this.ruleForm.tableName = [];
      if (val) {
        this.tableNames.forEach(item => {
          this.ruleForm.tableName.push(item.value);
        });
      }
    },

    selectChangeDB(val) {
      this.value = val;
      this.$forceUpdate();
      this.checkDBDriver(val);
    },

    checkDBDriver(val) {
      if (val == "mongodb") {
        this.isShowEmbedModel = false;
      } else {
        this.isShowEmbedModel = true;
      }
    },

    showMutiRepoDir() {
      this.$refs.showRepoDir.isShowMutiDialog = true;
    },
    showMonoRepoDir() {
      this.$refs.showRepoDir.isShowMonoDialog = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.tooltip-content b {
  color: #bb6f00;
}

.title a {
  text-decoration: none;
}
.title a:hover {
  text-decoration: underline;
}
</style>



