<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3 style="color: #484848;">{{ title }}</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="module name" prop="moduleName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.moduleName"
              placeholder="please type the name of the module"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                For example, the generated code will be<br />
                moved to the <b>user</b> server code directory,<br />
                fill in the module name of the go.mod file<br />
                corresponding to the <b>user</b> server here.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item
            label="server name"
            prop="serverName"
            v-if="ruleForm.suitedMonoRepo"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.serverName"
              placeholder="required field"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                For example, if the generated code is to be moved to the
                <b>user</b><br />
                server code directory, fill in <b>user</b> here.<br />
                If the name contains hyphens, they will be converted to
                underscores.<br />
                example: your-server 🡆 your_server
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="cache name" prop="cacheName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.cacheName"
              placeholder="e.g. userToken"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="cache key prefix"
            prop="prefixKey"
            v-if="ruleForm.isNeedCustomKeyPrefix"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.prefixKey"
              placeholder="e.g. user:token"
            ></el-input>
          </el-form-item>
          <el-form-item label="key name" prop="keyName">
            <el-input
              style="width: 40%; font-size: medium"
              v-model.trim="ruleForm.keyName"
              placeholder="e.g. id"
            ></el-input>
            <el-select
              style="width: 40%; font-size: medium"
              v-model="ruleForm.keyType"
              placeholder="Go type of key"
              filterable
              allow-create
              clearable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in keyTypes"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item label="value name" prop="valueName">
            <el-input
              style="width: 40%; font-size: medium"
              v-model.trim="ruleForm.valueName"
              placeholder="e.g. token"
            ></el-input>
            <el-select
              style="width: 40%; font-size: medium"
              v-model="ruleForm.valueType"
              placeholder="Go type of value"
              filterable
              allow-create
              clearable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in valueTypes"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item label=" custom cache key prefix ">
            <el-switch v-model="ruleForm.isNeedCustomKeyPrefix"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                The default key name stored in the cache service is
                <b>cache name: key value</b><br /><br />
                If this option is enabled, setting a custom cache key prefix
                will change the<br />key name to
                <b>custom cache key prefix: key value</b>.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="large repository type">
            <el-switch v-model="ruleForm.suitedMonoRepo"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Disabled by default, it indicates that the generated module
                code<br />
                is only suitable for
                <b>monolithic application repository (monolith)</b><br />
                or <b>microservice multiple repository (multi-repo)</b> type
                projects.<br /><br />
                If enabled, it indicates that the generated module code is
                only<br />
                suitable for
                <b>microservice monolithic repository (mono-repo)</b><br />
                type projects.
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">Reset</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isDownloading"
            :loading="isDownloading"
            >Download code</el-button
          >
          <DownloadCode
            ref="downloadCode"
            @update-is-downloading="updateIsDownloading"
          ></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "WebCache",
  data() {
    return {
      title: "Generate cache code",
      path: "web-cache",
      helpInfo: `
      <p>Generate Key-Value cache (redis) code, for more development details, please refer to the <a href="https://go-sponge.com/guide/module-code/gen-cache.html" target="_blank">Development Guide</a>.</p>
      <br />
      <p>Move the folder <b>internal</b> to your project code folder.</p>
      `,
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isDownloading: false,

      ruleForm: {
        moduleName: "",
        serverName: "",
        cacheName: "",
        prefixKey: "",
        keyName: "",
        keyType: "",
        valueName: "",
        valueType: "",
        suitedMonoRepo: false,
        isNeedCustomKeyPrefix: false,
      },
      rules: {
        moduleName: [
          {
            required: true,
            message: "please type the name of the go module",
            trigger: "blur",
          },
        ],
        cacheName: [
          {
            required: true,
            message: "please type the cache name",
            trigger: "blur",
          },
        ],
        // prefixKey: [
        //   {
        //     required: true,
        //     message: "please type the cache key prefix name",
        //     trigger: "blur",
        //   },
        // ],
        keyName: [
          {
            required: true,
            message: "please type the key name",
            trigger: "blur",
          },
        ],
        keyType: [
          {
            required: true,
            message: "please select the go type of key",
            trigger: "blur",
          },
        ],
        valueName: [
          {
            required: true,
            message: "please type the value name",
            trigger: "blur",
          },
        ],
        valueType: [
          {
            required: true,
            message: "please select the go type of value",
            trigger: "blur",
          },
        ],
      },
      keyTypes: [
        { label: "uint64", value: "uint64" },
        { label: "string", value: "string" },
        { label: "int", value: "int" },
        { label: "int64", value: "int64" },
      ],
      valueTypes: [
        { label: "uint64", value: "uint64" },
        { label: "string", value: "string" },
        { label: "int", value: "int" },
        { label: "int64", value: "int64" },
      ],
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          var arg = "";
          if (this.ruleForm.keyType == "") {
            this.$message.error("Go type of the key cannot be empty");
            return false;
          }
          if (this.ruleForm.valueType == "") {
            this.$message.error("Go type of the value cannot be empty");
            return false;
          }
          if (this.ruleForm.suitedMonoRepo) {
            if (this.ruleForm.serverName == "") {
              this.$message.error("server name cannot be empty");
              return false;
            }
            arg = `web cache --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --cache-name=${this.ruleForm.cacheName} --prefix-key=${this.ruleForm.prefixKey} --key-name=${this.ruleForm.keyName} --key-type=${this.ruleForm.keyType} --value-name=${this.ruleForm.valueName} --value-type=${this.ruleForm.valueType} --suited-mono-repo=${this.ruleForm.suitedMonoRepo}`;
          } else {
            arg = `web cache --module-name=${this.ruleForm.moduleName} --cache-name=${this.ruleForm.cacheName} --prefix-key=${this.ruleForm.prefixKey} --key-name=${this.ruleForm.keyName} --key-type=${this.ruleForm.keyType} --value-name=${this.ruleForm.valueName} --value-type=${this.ruleForm.valueType} --suited-mono-repo=${this.ruleForm.suitedMonoRepo}`;
          }
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    clearFormData() {
      this.ruleForm = {};
    },

    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(other)";
        this.$forceUpdate();
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.tooltip-content b {
  color: #bb6f00;
}
</style>



